<template>
    <h1 id="lec7-topic"> Neural Networks: Optimization II </h1>
    <div class='author'><p> Author: Pierre, Haocheng Han, Tianhao Wang, Pierre, <b>Abbey</b> </p></div >
    <div class='author'><p> Reviewer: Eason, Abbey, Tianhao Wang </p></div >
    <p class="preface">
        Preface: Welcome to our new member <b>Abbey</b> who is very good at writing notes. We are so glad that you joined us!
    </p>

    <h2>Incremental Update and SGD <span class="paragraph_author"> (Abbey)</span></h2>
    <p> Recall Gradient Descent (GD) from Lecture 4, we predicted the training data set using the current state of the model,
        estimated the error with the average divergence (empirical risk) between the <b>predictions</b> and the <b>actual labels</b>, and
        finally updated the model weights with the estimated error gradient. This method is also called <b>Full-Batch Gradient Descent</b>, which
        takes <b>all</b> training data into account for each update. Intuitively, for GD algorithms, <b>the more training instances it includes,
        the more accurate its estimated error gradient will be</b>. As a result, since Full-Batch GD takes all instances into account,
        its single update can improve the model's performance with the highest probability.
    </p>

    <img class='image' src="./assets/full_batch_GD.png"/><br><br>
    <p>However, the accuracy of Full-Batch GD comes with a cost:</p>
    <ul>
        <li>1. If the dataset size N = 1 million and a single pass through the entire training data is call an <b>"epoch"</b>, then we can only make a
            single weight adjustment within one epoch. This is extremely computationally expensive.
        </li>
        <li>2. Stability can also be problematic. Although the full-batch gradient gives the most stable approximation to the true gradient, the
            lack of noisy steps makes this algorithm more susceptible to local minima (see more discussion afterward).
        </li>
    </ul>

    <p> To overcome these issues, we can alternatively process one data point at a time <b>(Incremental Updates)</b>. That is, within one epoch over
        1 million data points, we perform update on <MathJax :str="'\\( w \\)'" display="inline"/> <b>1 million</b> times as opposed to only one time in Full-Batch GD.
    </p>
    <img class='image' src="./assets/incremental_update.png"/><br><br>

    <p>Intuitive explanations of why Incremental Updates works:</p>
    <ul>
        <li>1. Since it processes only one data point at a time, it is much computationally faster and memory efficient.</li>
        <li>2. Frequent updates might result in noisier gradients, leading to a oscillation behavior (see graph below), which helps avoid/escape from the local minima.</li>
    </ul>
    <br>
    <img class='image' src="./assets/GDs_compare.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h3>Order of Presentation and SGD</h3>
    <p> Nevertheless, the Incremental Update Algorithm also has a potential drawback: for each epoch, the algorithm goes through all data points
        in the <b>same</b> order, which may lead to an unpleasant <b>cyclic behavior</b>, i.e., the influences of various data points might cancel out
        with each other, so the model might not make meaningful progress.
    </p>
    <img class='image' src="./assets/cyclic_behavior.png"/>

    <p> To alleviate this problem, we should <b>randomly shuffle</b> the training data at the beginning of each epoch. Since the order of data points
        is different each time, the algorithm gets a more convergent behavior. This combination of Incremental Update and Random Shuffling yields the
        algorithm named <b>Stochastic Gradient Descent (SGD)</b>, which has the pseudocode below:
    </p>
    <img class='image' src="./assets/SGD_code.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec7-sec-1"> Full-Batch vs SGD Explanation <span class="paragraph_author"> (Tianhao, Eason) </span></h2>
    <h3>Extreme Example</h3>
    <p> From the above sections, we learned the differences between Full-Batch GD and SGD. Practically,
        we would prefer SGD to Full-Batch GD due to its much faster convergence rate.
    </p>
    <p> For example, there are two curves in the graph below. The blue one represents the real <MathJax :str="'\\( f(x) \\)'" display="inline"/>,
        and the red one is our guess <MathJax :str="'\\( g(x) \\)'" display="inline"/>. Now suppose there are 100 identical instances used to to
        improve <MathJax :str="'\\( g(x) \\)'" display="inline"/> by applying either Full-Batch GD or SGD. For Full-Batch GD, it would go through all
        training data within one epoch to obtain an average gradient <MathJax :str="'\\( g_{avg} = \frac{g_1 + g_2 + ... + g_{100}}{100} \\)'" display="inline"/>
        and update <MathJax :str="'\\( w \\)'" display="inline"/> for only once. Since all 100 instances are identical,
        so <MathJax :str="'\\(  g_{avg} = g_1 = g_2 = ... = g_{100} \\)'" display="inline"/>. However, for SGD, it  would accumulate the gradient
        100 times within on epoch, i.e., <MathJax :str="'\\(  g_{sgd} = g_1 + g_2 + ... + g_{100} = 100 * g_{avg} \\)'" display="inline"/>. This means SGD
        achieves an effect 100 times greater than the Full-Batch GD per epoch, so it converges much faster. In practice, we may barely face the above extreme scenario,
        but we still meet with clumpy data very often, so the benefits of SGD remains.
    </p>
    <img class='image' src="./assets/full_batch_vs_SGD.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h3>The Issue of SGD - High Variance</h3>
    <p> SGDFull-Batch GD - (high variance).</p>
    <p> lectureg(X; W), Wf(x),
        i.e. minimize the expected divergence D between g(X; W) and f(X), D = div[g(X_{i}); W), f(X_{i})].
        g(X; W)f(X)total divergence (X_idivergence)
    </p>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-1"> Limitations of Backprop <span class="paragraph_author"> (Eason, Pierre) </span></h2>

    <!-- ======================================================================================================================================================================= -->
    <h2>Algorithms for Faster Convergence <span class="paragraph_author"> (Haocheng)</span></h2>
    <p>
        Vanilla GDs update model parameters entirely in the direction of its gradient, (lack reason) which is susceptible to local optima or saddle
        points. To solve this issue and improve convergence speed, we will introduce several variants of GD in this section.
    </p>

    <h3>Momentum</h3>
    <p>
        Just like what we learned from Lecture 6, the momentum method mimics a real life example that a hard ball will accelerate as it rolls down
        from the top of a mountain. With high speed, the ball is less likely to be trapped in the middle of the downhill (it may fly over) and deviates
        from its original rolling path. During training, model parameters are the hard balls rolling down from the mountain of 'loss function'. We can write
        the update rule in following:
    </p>
    <p>
        <MathJax :str="'\\( m_t = \\beta*m_{t-1} + \\eta*\\nabla E(w) \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( w_{t+1} = w_{t} - \\alpha*m_{t}  \\)'" display="inline"/>
    </p>
    <p>
        One pro of the momentum method is its faster convergence by accumulating 'speed' of the previous update, but with high accumulated 'speed', each update
        may deviate from original path too much. To alleviate this issue, the algorithm Nestorov's Accelerated Gradient is introduced.
    </p><br>

    <h3>Nestorov's Accelerated Gradient (NAG)</h3>
    <p>
        NAG improves the Momentum method to make the update process less wild. As shown in Figure, NAG first extends the previous step (yellow arrow), and then
        calculate the gradient at the resultant position (orange arrow). By adding yellow and orange arrows, the resultant red arrow is the update step.
    </p>
    <p>
        Mathematically, NAG update rule can be expressed as:
    </p>
    <p>
        <MathJax :str="'\\( g_t=\\nabla E(w_{t-1}-\\alpha\\beta m_{t-1})  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( m_{t}=\\beta m_{t-1} + g_t  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( w_{t+1} = w_{t} - \\alpha*m_{t}   \\)'" display="inline"/>
    </p>

    <h2>Adding Second-Order Term (Tianhao, Eason)</h2>
    <p>
        Both Momentum method and NAG improves convergence by normalizing the mean of derivatives. However, we can take a step further by considering their variances.
        For example, in figure below, there is a general oscillating convergent path for Momentum method or NAG, and we notice that the amount of vertical movement weights
        a large portion of total movements even though we favor horizontal movement. By considering variances of derivatives, we can then decrease the step size in directions
        with high motion, i.e. vertical direction and scale up favored directions, i.e. horizontal direction to improve convergence.
    </p>

    <h3>RMSprop</h3>
    <p>
        RMSprop (Root Mean Square prop) updates each individual parameter and scales down/up updates with large/small mean squared derivatives by recording its running estimate (history of parameter motions).
    </p>
    <p>
        <MathJax :str="'\\( V_t= \\beta *V_{t-1} + (1-\\beta) * \\nabla E(w)^2  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( w_{t+1} = w_t - \\frac{\\eta}{\\sqrt{V_t + \epsilon}}\\nabla E(w)  \\)'" display="inline"/>
    </p>

    <h3>Adam</h3>
    <p>Adam improves RMSprop by taking momentum into consideration as follows:</p>
    <p>
        <MathJax :str="'\\( m_t = \\beta_1*m_{t-1} + (1-\\beta_1)*\\nabla E(w)  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( V_t= \\beta_2 *V_{t-1} + (1-\\beta_2) * \\nabla E(w)^2  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( \\hat{m}_t = \\frac{m_t}{1-\\beta_1}  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( \\hat{V}_t = \\frac{V_t}{1-\\beta_2}  \\)'" display="inline"/>
    </p>
    <p>
        <MathJax :str="'\\( w_{t+1} = w_t - \\frac{\\eta}{\\sqrt{\\hat{V}_t + \\epsilon}}\\hat{m}_t  \\)'" display="inline"/>
    </p>

