<template>
    <div id="PentagonContainer">
        <canvas id="pentagon"></canvas>
        <canvas id="pentagonMLP"></canvas>
    </div>

    <p class="img_description"> Figure 2: An MLP architecture represents a pentagon.</p>
</template>

<script>
import { onMounted } from 'vue'
import { pentagonMLP } from '@/engine/binary.js'
import { Polygon } from '@/engine/polygon.js'

export default {
    name: 'PentagonGraph',
    setup () {
        onMounted(() => {
            function adjustFigure (containerId, canvasIds, ratio) {
                var binarywidth = document.getElementById(containerId).clientWidth
                var binaryheight = binarywidth * ratio
                document.getElementById(containerId).setAttribute('style', 'height:' + binaryheight + 'px')
                for (var i in canvasIds) {
                    document.getElementById(canvasIds[i]).width = binaryheight
                    document.getElementById(canvasIds[i]).height = binaryheight
                    document.getElementById(canvasIds[i]).setAttribute('style', 'width:' + binaryheight + 'px')
                    document.getElementById(canvasIds[i]).setAttribute('style', 'height:' + binaryheight + 'px')
                }
            }

            // Pentagon
            adjustFigure('PentagonContainer', ['pentagon', 'pentagonMLP'], 0.492)
            Polygon(5, 'pentagon')
            pentagonMLP()

            window.addEventListener('resize', function () {
                adjustFigure('PentagonContainer', ['pentagon', 'pentagonMLP'], 0.492)
                Polygon(5, 'pentagon')
                pentagonMLP()
            })
        })

        function mouseMove (e) {
        }

        function mouseClick () {
        }

        return {
            mouseMove,
            mouseClick,
            onMounted
        }
    }
}
</script>

<style scoped>
p.binary_input, p.n_side_input, p.caption {
    text-align: center;
}

#PentagonContainer{
    display: block;
    width:100%;
}

#pentagon{
    display: inline;
    border: 2px solid rgb(190, 187, 187);
}

#pentagonMLP{
    display: inline;
    border: 1px solid rgb(190, 187, 187);
    margin-left: 1%;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
