import * as util from '@/utils/util.js'
export function initialBinary () {
    var initial = document.getElementById('binary')
    var initalC = initial.getContext('2d')
    var w = document.getElementById('binary').offsetWidth
    var h = document.getElementById('binary').offsetHeight

    // console.log(w, h)

    initalC.clearRect(0, 0, w, h)

    initalC.beginPath()
    initalC.moveTo(0, h / 2)
    initalC.lineTo(w, h / 2)
    initalC.moveTo(w / 2, 0)
    initalC.lineTo(w / 2, h)
    initalC.strokeStyle = 'black'
    initalC.lineWidth = 2
    initalC.globalAlpha = 1.0
    initalC.closePath()
    initalC.stroke()

    // red dots
    var radius = 0.01 * Math.min(w, h)
    var reds = [{ x: 0.1 * w, y: 0.3 * h }, { x: 0.2 * w, y: 0.2 * h }, { x: 0.05 * w, y: 0.4 * h },
        { x: 0.2 * w, y: 0.2 * h }, { x: 0.3 * w, y: 0.1 * h }, { x: 0.15 * w, y: 0.6 * h },
        { x: 0.4 * w, y: 0.4 * h }, { x: 0.35 * w, y: 0.45 * h }, { x: 0.25 * w, y: 0.35 * h },
        { x: 0.1 * w, y: 0.8 * h }, { x: 0.1 * w, y: 0.7 * h }, { x: 0.12 * w, y: 0.45 * h }]

    initalC.beginPath()
    for (var i = 0; i < reds.length; i++) {
        initalC.moveTo(reds[i].x, reds[i].y)
        initalC.arc(reds[i].x, reds[i].y, radius, 0, 2 * Math.PI)
        initalC.strokeStyle = 'red'
        initalC.fillStyle = 'red'
        initalC.globalAlpha = 1.0
        initalC.fill()
    }
    initalC.stroke()

    // blue dots
    radius = 0.01 * Math.min(w, h)
    var blues = [{ x: 0.5 * w, y: 0.5 * h }, { x: 0.6 * w, y: 0.8 * h }, { x: 0.75 * w, y: 0.55 * h },
        { x: 0.7 * w, y: 0.9 * h }, { x: 0.65 * w, y: 0.3 * h }, { x: 0.64 * w, y: 0.45 * h },
        { x: 0.9 * w, y: 0.8 * h }, { x: 0.55 * w, y: 0.85 * h }, { x: 0.94 * w, y: 0.83 * h },
        { x: 0.6 * w, y: 0.7 * h }, { x: 0.65 * w, y: 0.6 * h }, { x: 0.64 * w, y: 0.64 * h }]

    initalC.beginPath()
    for (i = 0; i < blues.length; i++) {
        initalC.strokeStyle = 'blue'
        initalC.fillStyle = 'blue'
        initalC.moveTo(blues[i].x, blues[i].y)
        initalC.arc(blues[i].x, blues[i].y, radius, 0, 2 * Math.PI)
        initalC.globalAlpha = 1.0
        initalC.fill()
    }
    initalC.stroke()

    // text
    initalC.font = w * 0.05 + 'px Arial'
    initalC.fillStyle = 'black'
    initalC.textAlign = 'right'
    initalC.fillText('(0,  10)', w * 49 / 100, h * 5 / 100)
    initalC.textAlign = 'left'
    initalC.fillText('(0, -10)', w * 51 / 100, h * 98 / 100)
    initalC.textAlign = 'left'
    initalC.fillText('(-10, 0)', w / 100, h * 55 / 100)
    initalC.textAlign = 'right'
    initalC.fillText('(10,  0)', w * 99 / 100, h * 48 / 100)
}

export function Activate () {
    var c = document.getElementById('binary').getContext('2d')
    var w = document.getElementById('binary').offsetWidth
    var h = document.getElementById('binary').offsetHeight

    var wx = document.getElementById('wx').value
    var wy = document.getElementById('wy').value
    var bias = document.getElementById('bias').value

    if (isNaN(wx) || wx === '') { wx = 0 }
    if (isNaN(wy) || wy === '') { wy = 0 }
    if (isNaN(bias) || bias === '') { bias = 0 }
    if (wx === 0 && wy === 0) { return }

    wx = Number(wx)
    wy = Number(wy)
    bias = Number(bias)

    var Nodes = []
    // Top y = 10
    // x = (bias-wy*10)/wx
    var Top = (bias - wy * 10) / wx
    var rTop = Math.max(Math.min(10, Top), -10)
    if (rTop === Top) { Nodes.push({ x: w / 2 * (1 + Top / 10), y: 0 }) }

    // Right x = 10
    // y = (bias-wx*10)/wy
    var Right = (bias - wx * 10) / wy
    var rRight = Math.max(Math.min(10, Right), -10)
    if (rRight === Right) { Nodes.push({ x: w, y: h / 2 * (1 - Right / 10) }) }

    // Bottom y = -10
    // x = (bias+wy*10)/wx
    var Bottom = (bias + wy * 10) / wx
    var rBottom = Math.max(Math.min(10, Bottom), -10)
    if (rBottom === Bottom) { Nodes.push({ x: w / 2 * (1 + Bottom / 10), y: h }) }

    // Left x = -10
    // y = (bias+wx*10)/wy
    var Left = (bias + wx * 10) / wy
    var rLeft = Math.max(Math.min(10, Left), -10)
    if (rLeft === Left) { Nodes.push({ x: 0, y: h / 2 * (1 - Left / 10) }) }

    // Points
    initialBinary()

    c.beginPath()
    c.moveTo(Nodes[0].x, Nodes[0].y)
    c.lineTo(Nodes[1].x, Nodes[1].y)
    c.closePath()
    c.strokeStyle = 'black'
    c.lineWidth = 2
    c.globalAlpha = 1.0
    c.stroke()
}

function DrawLine (start, end, c, color) {
    c.beginPath()
    c.moveTo(start.x, start.y)
    c.lineTo(end.x, end.y)
    c.strokeStyle = color
    c.lineWidth = 0.5
    c.globalAlpha = 1.0
    c.closePath()
    c.stroke()
}

function DrawConnection (start, end, c, color) {
    var node1 = { x: start.x + start.width, y: start.y }
    var node2 = { x: end.x - end.width, y: end.y }
    DrawLine(node1, node2, c, color)
}

function DrawCircle (loc, radius, c, color) {
    c.beginPath()
    c.fillStyle = color
    c.moveTo(loc.x, loc.y)
    c.arc(loc.x, loc.y, radius, 0, 2 * Math.PI, false)
    c.stroke()
    c.globalAlpha = 1.0
    c.fill()
}

function DrawLayer (locs, radius, c, color) {
    for (var i = 0; i < locs.length; i++) {
        DrawCircle(locs[i], radius, c, color)
    }
}

export function DrawMLP (id, arch) {
    var c = document.getElementById(id).getContext('2d')
    var w = document.getElementById(id).offsetWidth
    var h = document.getElementById(id).offsetHeight

    var numLayer = arch.length
    var interval = w / (numLayer + 1)
    var modelArchs = []
    var modelArch = []

    // Input layer
    var inputWidth = Math.min(interval, w / (arch[0] + 1)) / 2 * 0.2
    var inputLocs = []
    for (var i = 0; i < arch[0]; i++) {
        inputLocs.push({ x: interval, y: w / (arch[0] + 1) * (i + 1) })
        modelArch.push({ x: interval, y: w / (arch[0] + 1) * (i + 1), width: inputWidth })
    }
    modelArchs.push(modelArch)

    DrawLayer(inputLocs, inputWidth, c, util.orange)

    // Models Architecture
    var modelWidth = 0
    var modelLocs = []
    var modelLoc = []
    for (var l = 1; l < numLayer - 1; l++) {
        modelLoc = []
        modelArch = []
        modelWidth = Math.min(Math.min(interval, w / (arch[l] + 1)) / 2 * 0.8, 2 * inputWidth)
        for (i = 0; i < arch[l]; i++) {
            modelLoc.push({ x: interval * (l + 1), y: w / (arch[l] + 1) * (i + 1) })
            modelArch.push({ x: interval * (l + 1), y: w / (arch[l] + 1) * (i + 1), width: modelWidth })
        }
        DrawLayer(modelLoc, modelWidth, c, util.purple)
        modelLocs.push(modelLoc)
        modelArchs.push(modelArch)
    }

    // Output layer
    var outputWidth = Math.min(interval, w / (arch[0] + 1)) / 2 * 0.2
    var outputLocs = []
    modelArch = []
    for (i = 0; i < arch[numLayer - 1]; i++) {
        outputLocs.push({ x: interval * numLayer, y: w / (arch[numLayer - 1] + 1) * (i + 1) })
        modelArch.push({ x: interval * numLayer, y: w / (arch[numLayer - 1] + 1) * (i + 1), width: outputWidth })
    }
    modelArchs.push(modelArch)

    DrawLayer(outputLocs, outputWidth, c, util.wineRed)

    // Connections
    for (l = 0; l < numLayer - 1; l++) {
        for (var a = 0; a < modelArchs[l].length; a++) {
            for (var b = 0; b < modelArchs[l + 1].length; b++) {
                DrawConnection(modelArchs[l][a], modelArchs[l + 1][b], c, 'black')
            }
        }
    }
}

export function ActivateMLP () {
    var c = document.getElementById('MLP').getContext('2d')
    var w = document.getElementById('MLP').offsetWidth
    var h = document.getElementById('MLP').offsetHeight
    c.clearRect(0, 0, w, h)

    var arch = document.getElementById('arch').value
    arch = arch.split(',')

    if (arch.length === 0) arch = ['1']
    if (arch.length === 1) arch.push('1')

    for (var l = 0; l < arch.length; l++) {
        if (isNaN(arch[l]) || arch[l] === '') { arch[l] = 1 } else { arch[l] = Number(arch[l]) }
    }

    DrawMLP('binaryMLP', arch)
}

// Default
export function binaryMLP () {
    var c = document.getElementById('binaryMLP').getContext('2d')
    var w = document.getElementById('binaryMLP').offsetWidth
    var h = document.getElementById('binaryMLP').offsetHeight
    var arch = [2, 1, 1]
    c.clearRect(0, 0, w, h)
    DrawMLP('binaryMLP', arch)

    // Notation
    c.font = w * 0.05 + 'px Arial'
    c.fillStyle = 'black'
    c.textAlign = 'right'
    c.fillText('input x', w * 20 / 100, h * 34.5 / 100)
    c.fillText('input y', w * 20 / 100, h * 69 / 100)
    c.fillText('output', w * 92 / 100, h * 51.5 / 100)

    // Weights
    var wx = document.getElementById('wx').value
    var wy = document.getElementById('wy').value
    var bias = document.getElementById('bias').value

    c.font = w * 0.05 + 'px Arial'
    c.fillStyle = 'black'
    c.textAlign = 'right'
    c.fillText(wx, w * 40 / 100, h * 40 / 100)
    c.fillText(wy, w * 40 / 100, h * 65 / 100)
    c.textAlign = 'left'
    c.fillText('bias = ' + bias, w * 55 / 100, h * 57 / 100)
}

export function pentagonMLP () {
    var c = document.getElementById('pentagonMLP').getContext('2d')
    var w = document.getElementById('pentagonMLP').offsetWidth
    var h = document.getElementById('pentagonMLP').offsetHeight
    var arch = [2, 5, 1, 1]
    c.clearRect(0, 0, w, h)
    DrawMLP('pentagonMLP', arch)

    // Notation
    c.font = w * 0.05 + 'px Arial'
    c.fillStyle = 'black'
    c.textAlign = 'right'
    c.fillText('input x', w * 17 / 100, h * 35 / 100)
    c.fillText('input y', w * 17 / 100, h * 68.5 / 100)
    c.fillText('output', w * 97 / 100, h * 51.5 / 100)
    c.fillText('AND', w * 75 / 100, h * 57 / 100)
}
