<template>
    <h1 id="lec6-topic"> Neural Networks: Optimization I </h1>
    <div class='author'><p> Author: Pierre, Haocheng Han, Tianhao Wang, Pierre, Ravi, Eason, <b>Yinghao Ma</b> </p></div >
    <div class='author'><p> Reviewer: Eason, Tianhao Wang </p></div >

    <p class="preface">
        Preface: So in this note, we will assume that you've already had the background of lecture 5. Additionally, we wish to express our greatest
        appreciation to our new member <b>Yinghao</b> who joined at our most difficult time. Thank you so much, Yinghao!
    </p><br>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-1"> Limitations of Backprop <span class="paragraph_author"> (Eason, Pierre) </span></h2>
    <p> By learning backpropagation in lecture 5, we have witnessed that this technology can optimize the model performance by minimizing convergence,
        thereby solving many practical problems. However, backprop also has limitations: <b>minimizing divergence may not minimize classification error</b> (this is what we value most).
    </p>

    <p> For example, in the figure below, there are three red clusters and one blue cluster, and they are obviously linearly separable. Thus the perceptron can trivially find the
        decision boundary <span style="color:blue">(blue line)</span> to obtain the best accuracy. However, if we use backprop to minimize the
        <MathJax :str="MSE" display="inline"/>,
        its optimal decision boundary cannot perfectly separate the data. This is because MSE essentially minimizes the <b>average distance</b> between the data point and
        decision boundary, and the <span style="color:red">(red line)</span> obviously achieves the minimal average distance. In conclusion, minimizing divergence cannot guarantee an optimal classification accuracy.
    </p>
    <img class='image' src="./assets/backprop.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h3 id="lec6-sec-1-1">Comparison between the Perceptron and Backprop Algorithms</h3>
    <p> Now, let's compare Perceptron and Backprop from the perspective of the
        <button class='hyperlink' v-scroll-to="{ element: '#lec6-sec-6-1', container: '#noteContainer', easing: 'linear' }"> Bias-Variance</button>:
    </p>

    <p><b>Perceptron:</b></p>
    <ul>
        <li>1. It can achieve low bias <span style="color:green">(Pros)</span></li>
        <li>2. Risk of having high variance <span style="color:red">(Cons)</span></li>
        <li>3. It is rather easy to overfit on data <span style="color:red">(Cons)</span></li>
    </ul>

    <p><b>Backprop:</b></p>
    <ul>
        <li>1. It can achieve low variance <span style="color:green">(Pros)</span></li>
        <li>2. Risk of having high bias <span style="color:red">(Cons)</span></li>
        <li>3. It is possible to underfit the training data <span style="color:red">(Cons)</span></li>
    </ul>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-2"> Loss Surfaces <span class="paragraph_author"> (Yinghao, Tianhao) </span></h2>
    <p>So far, we have learned the basic of Gradient Descent and Backpropagation. In this section, we will discuss how GD performs on <b>convex</b> and <b>non-convex</b> surfaces</p>

    <!-- ======================================================================================================================================================================= -->
    <h3 id="lec6-sec-2-1"> Convex Loss Surface </h3>
    <p> We call a loss surface a <b>"convex"</b> surface if it is continuously curving upward: we can connect any two points on or above the surface <b>without intersecting</b> it. </p>
    <img class='image' src="./assets/convex.png"/>
    <p> For a convex function, <b>any local minimum is also a global minimum.</b> Due to this lovely property, we always pray that our target loss function is convex since we can
        apply Gradient Descent to get its minimum easily. However, DL neural network surfaces are generally non-convex.</p>

    <!-- ======================================================================================================================================================================= -->
    <h3 id="lec6-sec-2-2">Non-convex Loss Surface</h3>
    <p> Compared to convex functions, non-convex functions are hard to optimize since their surface consists of many local minima and saddle points,
        where the gradient descent might get stuck. <b>Saddle point</b> are stable points where the function has a local maximum in one direction (along green line),
        but a local minimum in another direction (along orange line).
    </p>
    <img class='image' src="./assets/saddle_point.png"/>
    <p class="img_description"> https://en.wikipedia.org/wiki/Sphaleron#/media/File:Saddle_point.svg </p>

    <!-- ======================================================================================================================================================================= -->
    <h3 id="lec6-sec-2-3">Convergence Rate and Non-optimal Step Size</h3>
    <p> A machine learning model reaches convergence when it achieves a state during training in which loss settles within an error range around the final value. In other words,
        a model converges when additional training will not improve the model [1]. <b>Nonetheless, with an inappropriate convergence rate,</b> the algorithm may not actually
        converge but jitter around or even diverge from the local minimum.
    </p>
    <img class='image' src="./assets/CJD.png"/>

    <p> The Convergence rate <MathJax :str="R" display="inline"/> is an indicator of how fast the iterations arrive at the solution, where <MathJax :str="'\\( w^{(k)} \\)'" display="inline"/> is the value of
        <MathJax :str="'\\( w \\)'" display="inline"/> at the k-th iteration and <MathJax :str="'\\( w^{*} \\)'" display="inline"/> is the optimal value of <MathJax :str="'\\( w \\)'" display="inline"/>.
        <MathJax :str="'\\( R \\)'" display="inline"/> is determined by the  <MathJax :str="'\\( w \\)'" display="inline"/>'s step size which needs to be <b>selected carefully</b> since it determines if the algorithm could converge to the targeted minimum point.
        We will discuss it from <b>two perspectives:</b> univariate and multivariate functions.
    </p>

    <!-- ======================================================================================================================================================================= -->
    <h4 id="lec6-sec-2-3-1">1. Univariate Function</h4>
    <p> Taking the 1-D quadratic function <MathJax :str="'\\( E = \\frac{1}{2}aw^2+bw+c \\)'" display="inline"/> as an example, we could apply the Newton's method to obtain
        the best step size <MathJax :str="'\\( \\eta_{opt} = \\frac{1}{E\'\'\\left(w\\right)}=\\frac{1}{a} \\)'" display="inline"/>.
        This means it only needs a single step to reach the global minimum.
    </p>
    <img class='image' src="./assets/one_step.png"/>

    <p> On comparison, GD iteratively takes small steps to achieve the equivalent effect, <b>but step size need to be carefully selected.</b> As the pictures shown as follows,
        the algorithm will converge if <MathJax :str="'\\( \\eta < \\eta_{opt} \\)'" display="inline"/> without jittering and converge with jittering if
        <MathJax :str="'\\( \\eta_{opt}<\\eta<2\\eta_{opt} \\)'" display="inline"/>. Once <MathJax :str="'\\( \\eta > 2\\eta_{opt} \\)'" display="inline"/>,
        the algorithm moves away from the minimum. Consequently, choosing an appropriate step size is important and it shall less than
        <MathJax :str="'\\( 2\\eta_{opt} \\)'" display="inline"/>.
    </p>
    <img class='image' src="./assets/univariable.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h4 id="lec6-sec-2-3-2">2. Multivariate Function</h4>
    <p> From the previous section, we learn that for 1-D quatractic function, tunning <b>one varible</b> <MathJax :str="'\\( \\eta \\)'" display="inline"/> is enough to control its
        convergence behaviour due to its 1-D property. Nevertheless, this is not the case for multivariate functions and choosing an appropriate step size for them is <b>more challenging</b>.
    </p>
    <p> For instance, say we have a quadratic function <MathJax :str="'\\( E = \\frac{1}{2}w^T\\mathbf{A}w+w^T\\mathbf{b}+c \\)'" display="inline"/>. Since it has multiple variables,
        we have to find the optimal learning rate for each varible <MathJax :str="'\\( \\eta_{opt,i} \\)'" display="inline"/> along its corrsponding coordinate to obtain the best combo
        <MathJax :str="'\\( \\eta_{opt} \\)'" display="inline"/> for all learning rates. To see the point, we can assume
        <MathJax :str="'\\( \w \\)'" display="inline"/> is a 2D vector <MathJax :str="'\\( [w_1, w_2] \\)'" display="inline"/>,
        then <MathJax :str="'\\( E \\)'" display="inline"/> is a sum of two <b>independent</b> 1-D quadratic
        functions when <MathJax :str="'\\(\\mathbf{A}\\)'" display="inline"/> is a diagonal matrix. In such case, we can find two optimal learning rates
        <MathJax :str="'\\( \\eta_{opt,1} \\)'" display="inline"/> and <MathJax :str="'\\( \\eta_{opt,2} \\)'" display="inline"/> (one per 1D function), and
        they are independent with each other. To generalize, if  <MathJax :str="'\\( w \\)'" display="inline"/> is N-dimensional, then there are N such
        <MathJax :str="'\\( \\eta_{opt,i} \\)'" display="inline"/>.
    </p>

    <p> As discussed in the univariate case, step size <MathJax :str="'\\(\\eta\\)'" display="inline"/> in 1D quadratic function shall be smaller than <MathJax :str="'\\( 2\\eta_{opt} \\)'" display="inline"/>
        to guarantee finding a solution. Similarly, a multivariate function has N <MathJax :str="'\\(\\eta_{opt,i}\\)'" display="inline"/> for N correspoding dimentions,
        and each dimension's step size <MathJax :str="'\\( \\eta_{i} \\)'" display="inline"/> shall be smaller than <MathJax :str="'\\( 2\\eta_{opt,i} \\)'" display="inline"/>, so
        there are totally N constrants for <MathJax :str="'\\(\\eta\\)'" display="inline"/>. Additionally, the learning rate <MathJax :str="'\\(\\eta\\)'" display="inline"/>
        would apply to all sub-components, therefore a multivariate quadratic function <MathJax :str="'\\( \E \\)'" display="inline"/> converges if and only if
        <MathJax :str="'\\( \\eta \\)'" display="inline"/>  &#60; <MathJax :str="'\\( min(2\\eta_{opt,i}) \\)'" display="inline"/>, for <MathJax :str="'\\( i = 1, ... N \\)'" display="inline"/>
    </p>

    <p> For a general multivariate function, its convergence behaviors would become increasingly unpredictable as dimension increases (more constraints), making it even harder to find
        the best <MathJax :str="'\\( \\eta \\)'" display="inline"/>.
    </p>

    <!-- ======================================================================================================================================================================= -->
    <h3 id="lec6-sec-2-4"> Optimal Learning Rate is NOT UNIVERSAL </h3>
    <p> From previous section, we've learned that an appropriate <MathJax :str="'\\( \\eta \\)'" display="inline"/> of a multivariate function <MathJax :str="'\\(\E\\)'" display="inline"/>
        should be smaller than <MathJax :str="'\\( 2\\eta_{opt,i} \\)'" display="inline"/> in all dimensions. However, the convergence would be slow if the ratio of
        <MathJax :str="'\\( \\frac{\\max\\{\\eta_{opt,i}\\}}{\\min\\{\\eta_{opt,i}\\}} \\)'" display="inline"/>
        is large (see <span style="color:red">red line</span> in graph). To speed up convergence, a modified update rule should be applied:
    </p>
    <MathJax :str="'\\[ w^{(k+1)} = w^{(k)} - \\eta \\cdot \\mathbf{H}_{E}^{-1}\\nabla_w E\\left(w^{(k)}\\right)^T \\]'" display="block"/>

    <p> <MathJax :str="'\\( \\mathbf{H}_{E} \\)'" display="inline"/>, the second derivative of a multivariate function <MathJax :str="'\\( E \\)'" display="inline"/>
        with respect to <MathJax :str="'\\(w\\)'" display="inline"/>,  is called the Hessian Matrix. Adding an inverse Hessian matrix in the update rule can help to
        <b>scale down</b> <MathJax :str="'\\( \\frac{\\max\\{\\eta_{i,opt}\\}}{\\min\\{\\eta_{i,opt}\\}} \\)'" display="inline"/> so as to accelerate the convergence.
    </p>

    <img class='image' src="./assets/Coefficient_w0.png"/>
    <p class="img_description">
        GD (red line, perpendicular to the contour) and SGD (black line) are shown. While the optimal direction is the yellow arrow which
        is equal to the optimal step size <MathJax :str="'\\( \\hat{w} \\)'" display="inline"/> in every direction.
    </p>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-3"> Two Issues of the Faster Update Rule <span class="paragraph_author"> (Tianhao) </span></h2>
    <p> In the section <button class='hyperlink' v-scroll-to="{ element: '#lec6-sec-2-3-1', container: '#noteContainer', easing: 'linear' }"> Univariate Function </button>,
        We introduce you the Newtons ......... However, in most complex neural network models, the rule is hardly applied due to two issues:</p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-3-1">Issue 1: The Hessian</h3>
    <p> <b>Computing Hessian is expensive.</b> If a model has large number of parameters, it is almost infeasible to compute the Hessian and invert it because we will end up with a
        giant squared matrix with millions of rows/columns. In addition, if the objective is non-convex function, the Hessian may not be <b>positive semi-definite</b>
        <link needed>, so the algorithm will go away from the optimal solution.
    </p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-3-2">Issue 2: Learning Rate</h3>
    <p> In a complex neural network, the loss function is often not convex. In such case, having a fixed learning rate <MathJax :str="'\\( \\eta \\)'" display="inline"/>
        may make the optimization algorithms trapped in a local optima. </p>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-4"> Two Feasible Solutions <span class="paragraph_author"> (Tianhao) </span></h2>
    <p> To address the issue of Hessian computation, we can use algorithms such as BFGS to approximate the Hessian matrix instead of computing its exact value.</p>
    <p> Regarding the issue of local minima, we can initialize a large learning rate \eta, and then gradually decay it. In this way, while
        <MathJax :str="'\\( \\eta>2\\eta_{opt} \\)'" display="inline"/> at beginning of training, the method, we hope, can escape from a local optima.
        When <MathJax :str="'\\( \\eta \\)'" display="inline"/> decays and becomes less than <MathJax :str="'\\( \\eta_{opt} \\)'" display="inline"/>,
        the optimization algorithm will converge to a minimum.
    </p>
    <img class='image' src="./assets/local_minimum.png"/>

    <!-- ======================================================================================================================================================================= -->
    <h2 id="lec6-sec-5"> More Derivative-Inspired Algorithms <span class="paragraph_author"> (Eason, Tianhao) </span></h2>
    <p> As discussed before, static learning rate is likely to cause convergence issues.  In this section, we will
        experiment with four algorithms that utilize <b>dynamic learning rate</b> to see if they can alleviate this problem.
    </p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-5-1">Rprop</h3>
    <p>The first algorithm is called RProp, which is short for Resilient propagation. Rprop is a simple <b>first-order</b> optimization algoriths which makes a decision between two options each step:</p>
    <ul>
        <li>(1) At each iteration, if the derivative doesn't change its sign compared to the previous location, <b>increase</b> the step size to a longer step</li>
        <li>(2) On the other hand, if the sign changes. Return to the previous location and <b>shrink</b> the step. Then take a smaller step forward</li>
    </ul>

    <p> Suppose <MathJax :str="'\\( \\alpha \\)'" display="inline"/> is an increasing factor (empirically set to 1.2) and <MathJax :str="'\\( \\beta \\)'" display="inline"/> is a
        decreasing factor (empirically set to 0.5), the graph below shows how Rprop works.</p>
    <img class='image' src="./assets/Rprop.png"/>
    <p> In step 1, we move forward as normal. In step 2 and 3, since the derivatives'sign remain the same, so we increase the step size by
        <MathJax :str="'\\( \\alpha \\)'" display="inline"/> (1.2).
        However, we notice that the derivative changes its sign after step 3, so we return to the previous location (step 4) and shrink the step by
        <MathJax :str="'\\( \\beta \\)'" display="inline"/> (0.5).
        Then we take a smaller step forward as step 5 shows.
    </p>

    <p> This is the process for 1-dimensional optimization. For multi-dimensional optimization, Rprop implements this procedure for each dimensions independently. In nutshell,
        Rprop is a simple and efficient first-order optimization algorithm, which could achieve similar effects as some second-order methods with less computational cost.
    </p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-5-2">Quickprop</h3>
    <p> Quickprop it's a <b>second-order</b> method which is based on the Newton's method.
        A quick recap: the update rule of Newton's method is:
    </p>
    <MathJax :str="'\\[ w^{(k+1)} = w^{(k)} - \\eta H_E(w^{(k)})^{-1} \\nabla_wE(w^{(k)})^T \\]'" display="block"/>
    <p> As mentioned in earily sections, computing the inverse of the Hessian's matrix <MathJax :str="'\\( H_E(w^{(k)^{-1}}) \\)'" display="inline"/> is very expensive.
        Consequenly, we seldom directly use Newton's method for training in practice. To resolve this issue, Quickprop makes the following two modifications:
    </p>
    <ul>
        <li> 1. Similar to RProp, Quickprop computes the derivative for each dimension independently.</li>
        <li> 2. Quickprop <b>approximates</b> the Hessian's matrix as:
            <MathJax :str="'\\( H_E(w^{(k)})\\approx \\frac{E\'(w^{(k)})-E\'(w^{(k-1)})}{\\Delta w^{(k-1)}} \\)'" display="inline"/>.
            So instead of computing the second-order derivative directly, it uses the previous and current first-order derivative
            <MathJax :str="'\\( E\'(w^{(k)}) \\)'" display="inline"/> to estimate it. This greatly simplifies the the computation complexity.
        </li>
    </ul>
    <br><br>
    <p> The update rule of Quickprop is as follows:</p>
    <MathJax :str="'\\[ w^{(k+1)}_{l,i,j} =w^{(k)}_{l,i,j} - \\frac{\\Delta w^{(k-1)}_{l,i,j}}{E\'(w^{(k)}_{l,i,j})-E\'(w^{(k-1)}_{l,i,j})}\\nabla_wE(w^{(k)}_{l,i,j})^T \\]'" display="block"/>
    <p class="img_description"> <MathJax :str="'\\( w^{(k)}_{l,i,j} \\)'" display="inline"/> represents the weight of connection between node <MathJax :str="'\\( i \\)'" display="inline"/> in
        <MathJax :str="'\\( (l-1)^{th} \\)'" display="inline"/> layer and node <MathJax :str="'\\( j \\)'" display="inline"/> in the
        <MathJax :str="'\\( l^{th} \\)'" display="inline"/> layer, in <MathJax :str="'\\(  k^{th} \\)'" display="inline"/> iteration
    </p>
    <p>To sum up, Quickprop is an efficient and practical optimization algorithm: it approximates the functionality of the Newton's Method while saving a lot of computing time.</p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-5-3">Momentum</h3>
    <p> The third algorithm is called Momentum which also applies <b>dynamic learning rate</b> just like Rprop and QuickProp. Compared to most other optimization algorithms
        which only utilize the current gradient to guide the search, Momentum also combines the gradient from <b>previous step</b> to determine the next direction. This is where
        its name "Momentum" comes from - <b>utilize the previous accumulated momentum to avoid instant direction oscillations.</b>
    </p>

    <p> Now Imagine we are at the top of a mountain and our mission is to arrive its bottom. Since the mountain terrain is very rugged, the steepest gradient at each point is
        significantly different from each other. In this case, if we apply plain gradient descent which only considers the current gradient, we would suffer <b>frequent direction
        oscillations</b> due to these inconsistant gradients. As a result, it is impossible to descend the hill very fast.
    </p>

    <p> So, we are facing a dilemma. On the one hand, if we increase our step size, we would risk stronger oscillation and even divergence in a certain direction.
        On the other hand, if we decrease our step size, we are also moving slow. To overcome this issue, we introduce the <b>Momentum Method</b> which dampens the oscillations and
        accelerates the moving speed simultaneously, by maintaining a <b>running average of all the prior gradients:</b>
    </p>
    <img class='image' src="./assets/plain_GD.png"/>

    <h4> Working Principle</h4>
    <p> In the image below, we decompose the decent <MathJax :str="'\\(  v_{i} \\)'" display="inline"/> along
        <MathJax :str="'\\(  x \\)'" display="inline"/> and <MathJax :str="'\\(  y \\)'" display="inline"/> directions to demonstrate Momentum's principle.
    </p>
    <img class='image' src="./assets/how_momemtum.png"/>

    <p> As you may notice, during descent, the <MathJax :str="'\\(  Y \\)'" display="inline"/> component <MathJax :str="'\\(  v_{y_{i}} \\)'" display="inline"/>
        of each step are pointing in <b>opposite</b> directions. Thus, they nearly cancel out, minimizing the size of the oscillations.
        The <MathJax :str="'\\( X \\)'" display="inline"/> components point in the same direction reinforcing each other to move faster in the relevant direction of descent.
        Let's try to have a better intuition of the update rule:
    </p>
    <MathJax :str="'\\[ \\Delta W^{(k)} = \\beta W^{(k-1)} - \\eta (gradient) \\]'" display="block"/>
    <p> If the value of <MathJax :str="'\\( \\beta \\)'" display="inline"/> is much greater than <MathJax :str="'\\( \\eta \\)'" display="inline"/>,
        the previous gradients <MathJax :str="'\\( \\beta W^{(k-1)} \\)'" display="inline"/> will dominate the update rule and the gradient cannot change the descent direction quickly,
        so increasing <MathJax :str="'\\( \\beta \\)'" display="inline"/> is useful in a highly oscillatory descent.
        However, if <MathJax :str="'\\( \\beta \\)'" display="inline"/> is much less than <MathJax :str="'\\( \\eta \\)'" display="inline"/>, the running average of
        gradients slightly dampens the oscillations, which has a smoothing effect.
    </p>
    <p>Now, lets have a look at how the update take place. The parameter update takes place in two steps:</p>
    <p>1. The gradient step (<MathJax :str="'\\( -\\eta*gradient \\)'" display="inline"/>) shown in blue:</p>
    <img class='image' src="./assets/mom1.png"/>
    <p>2. Scaled previous step (<MathJax :str="'\\( \\beta\\Delta W^{(k-1)} \\)'" display="inline"/>) shown in red:</p>
    <img class='image' src="./assets/mom2.png"/>
    <img class='image' src="./assets/mom3.png"/>
    <p> The classical momentum algorithm can be understand from the example of a ball rolling down a bowl where
        the ball gradually gains 'momentum' in the direction of the descent.
    </p>

    <!-- *********************************************************************************************************************************************************************** -->
    <h3 id="lec6-sec-5-4"> Nestorovs Accelerated Gradient </h3>
    <p> In the previous example, the ball following the slope of the bowl is not the most optimized way of descent. A more optimized way of descent is when the
        ball develops the intuition that it is going to reach the bottom and that it has to slow down so as to not climb the slope up.
        Such an enhancement is provided by Nestorov's accelerated gradient (NAG) algorithm to the classical momentum algorithm.
    </p>
    <p> In NAG, we get a rough idea of where the next position of our parameters is going to be: </p>
    <MathJax :str="'\\[ (W^{(k-1)} + \\beta\\Delta W^{(k-1)}) \\]'" display="block"/>
    <img class='image' src="./assets/NAG1.png"/>
    <p> Once we have a rough idea where our next parameters are going to be, we can then calculate the gradient at that position: </p>
    <img class='image' src="./assets/NAG2.png"/>
    <p>The update rule for NAG is expressed as:</p>
    <MathJax :str="'\\[ W^{(k)} = \\beta\\Delta W^{(k-1)}-\\eta\\nabla_{w}Loss( W^{(k-1)}+\\beta\\Delta W^{(k-1)}) \\]'" display="block"/>
    <br><br>

    <h2 id="lec6-sec-6"> Appendix</h2>
    <h3 id="lec6-sec-6-1">Bias-Variance Tradeoff</h3>
    <p> Bias is the difference between the expected value and the predicted value. Variance is a type of error that occurs due to a model's
        sensitivity to small fluctuations in the training set. Either high bias or high variance error can greatly affect a model's performance.
    </p>
    <img class='image' src="./assets/bias_variance.png"/>
    <p> For example, <b>underfitting</b> occurs when the model makes generalization (high bias error) and results in a very simplistic model that does not consider
        the variation very well. Nevertheless, when the model learns too much from the training data and is incapable of generalizing well to the test data, it is called <b>overfitting.</b>
    </p>
    <br><br><br><br><br><br><br><br>
</template>

