            R: '\\(R = \\left|\\frac{f\\left(w^{(k+1)}\\right) - f\\left(w^*\\right)}{f\\left(w^{(k)}\\right) - f\\left(w^*\\right)}\\right|\\)',
            str: '\\x = {-b \\pm \\sqrt{b^2-4ac} \\over 2a}.\\)',
            str2: 'E = \\frac{1}{2}w^T\mathbf{A}w+w^T\mathbf{b}+c'
        }
    }
}
</script>

