<template>
    <div
        id="irCanvasContainter"
        @mousemove="mouseMove"
    >
    <canvas @mousemove="mouseMove" id='irCanvas' class="irreducible-map"></canvas>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { MLP } from '@/engine/MLP.js'
import * as util from '@/utils/util.js'

export default {
    name: 'IrreducibleMap',

    setup () {
        const mouseX = ref()
        const mouseY = ref()

        function mouseMove (e) {
            const canvas = document.getElementsByClassName('irreducible-map')[0]
            var bounds = canvas.getBoundingClientRect()
            mouseX.value = e.pageX - bounds.left - scrollX
            mouseY.value = e.pageY - bounds.top - scrollY
            mouseX.value /= bounds.width
            mouseY.value /= bounds.height
            mouseX.value *= canvas.width
            mouseY.value *= canvas.height
        }

        onMounted(() => {
            const canvasContainer = document.getElementById('irCanvasContainter')
            const canvas = document.getElementsByClassName('irreducible-map')[0]
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth
            canvas.height = canvasContainer.offsetHeight

            // irreducible map drawing
            var mapBlocks
            var mlp

            // mlp drawing
            var nodesDict = {}
            var linesArray = []

            function init (width, height) {
                linesArray = []
                nodesDict = {}
                mapBlocks = new IrreMap(40 / 493 * width, 40 / 450 * height, 70 / 493 * width, 140 / 450 * height)

                const layersInfo = [
                    {
                        numNodes: 4,
                        radius: 10,
                        color: '#db1c4c',
                        nodeTexts: ['W', 'X', 'Y', 'Z'],
                        textSize: 16 / 493 * width + 'px'
                    },
                    {
                        numNodes: 4,
                        radius: 10 / 493 * width,
                        color: '#BBBBBB'
                    },
                    {
                        numNodes: 2,
                        radius: 10 / 493 * width,
                        color: '#db1c4c'
                    },
                    {
                        numNodes: 2,
                        radius: 10 / 493 * width,
                        color: '#BBBBBB'
                    },
                    {
                        numNodes: 1,
                        radius: 10 / 493 * width,
                        color: '#db1c4c'
                    }
                ]
                const yIntervalArray = []
                for (var i = 0; i < layersInfo.length; i++) {
                    yIntervalArray.push(80 / 450 * height)
                }
                mlp = new MLP(canvas.width / 2 + 20, canvas.height / 2, layersInfo, 50 / 493 * width, yIntervalArray)
                mlp.addToEngine(nodesDict)
                mlp.connectTwoNodes('1-1', 0, '2-1', Math.PI, linesArray)
                mlp.connectTwoNodes('1-1', 0, '2-2', Math.PI, linesArray)
                mlp.connectTwoNodes('1-2', 0, '2-1', Math.PI, linesArray)
                mlp.connectTwoNodes('1-2', 0, '2-2', Math.PI, linesArray)
                mlp.connectTwoNodes('1-3', 0, '2-3', Math.PI, linesArray)
                mlp.connectTwoNodes('1-3', 0, '2-4', Math.PI, linesArray)
                mlp.connectTwoNodes('1-4', 0, '2-3', Math.PI, linesArray)
                mlp.connectTwoNodes('1-4', 0, '2-4', Math.PI, linesArray)
                mlp.connectTwoNodes('2-1', 0, '3-1', Math.PI, linesArray)
                mlp.connectTwoNodes('2-2', 0, '3-1', Math.PI, linesArray)
                mlp.connectTwoNodes('2-3', 0, '3-2', Math.PI, linesArray)
                mlp.connectTwoNodes('2-4', 0, '3-2', Math.PI, linesArray)
                mlp.connectTwoLayers('3', '4', linesArray)
                mlp.connectTwoLayers('4', '5', linesArray)
            }

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                init(canvas.width, canvas.height)
                mlp.addToEngine(nodesDict)
            })

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, canvas.width, canvas.height)

                // update irreducible map
                mapBlocks.draw(ctx, canvas.width, canvas.height)

                // update MLP
                for (var i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }
                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX.value, mouseY.value)
                }
            }
            init(canvas.width, canvas.height)
            animate()
        })
        return {
            onMounted,
            mouseX,
            mouseY,
            mouseMove
        }
    }
}

class IrreMap {
    constructor (w, h, x, y) {
        var arr = []
        for (var i = 0; i < 16; i++) {
            arr.push('')
        }
        this.contentArray = arr
        this.rows = 4
        this.cols = 4
        this.w = w
        this.h = h
        this.curPosition = {
            x: x,
            y: y
        }
        this.startPosition = this.curPosition
        this.color = util.grey
    }

    draw (c, width, height) {
        // draw block
        const hrows = [0, 0, 1, 1, 2, 2, 3, 3]
        const hcols = [0, 2, 1, 3, 0, 2, 1, 3]
        for (let i = 0; i < this.rows; i++) {
            for (let j = 0; j < this.cols; j++) {
                c.fillStyle = this.color
                c.fillRect(this.curPosition.x + i * this.w, this.curPosition.y + j * this.h, this.w, this.h)
            }
        }
        for (let i = 0; i < hrows.length; i++) {
            for (let j = 0; j < hcols.length; j++) {
                c.fillStyle = util.wineRed
                c.fillRect(this.curPosition.x + hcols[i] * this.w, this.curPosition.y + hrows[i] * this.h, this.w, this.h)
            }
        }

        // draw row lines
        c.lineWidth = 1
        for (let i = 0; i < this.rows + 1; i++) {
            c.beginPath()
            c.moveTo(this.startPosition.x, this.startPosition.y + i * this.h)
            c.lineTo(this.startPosition.x + this.w * this.cols, this.startPosition.y + i * this.h)
            c.strokeStyle = 'white'
            c.stroke()
        }
        // draw col lines
        for (let i = 0; i < this.cols + 1; i++) {
            c.beginPath()
            c.moveTo(this.startPosition.x + i * this.w, this.startPosition.y)
            c.lineTo(this.startPosition.x + i * this.w, this.startPosition.y + this.h * this.rows)
            c.strokeStyle = 'white'
            c.stroke()
        }

        // add text
        c.beginPath()
        c.moveTo(this.startPosition.x, this.startPosition.y)
        c.lineTo(this.startPosition.x - 50 / 493 * width, this.startPosition.y - 50 / 450 * height)
        c.strokeStyle = 'black'
        c.stroke()

        c.font = 15 / 493 * width + 'px Sans-serif'
        c.fillStyle = 'black'
        c.fillText('WX', this.curPosition.x - 50 / 493 * width, this.curPosition.y - 50 / 450 * height + 30 / 450 * height)
        c.fillText('YZ', this.curPosition.x - 50 / 493 * width + 30 / 493 * width, this.curPosition.y - 40 / 450 * height)

        const content = ['00', '01', '11', '10']
        c.textAlign = 'center'
        c.textBaseline = 'middle'
        c.font = (this.h / 2).toString() + 'px Sans-serif'
        for (let i = 0; i < content.length; i++) {
            c.fillText(content[i], this.curPosition.x - 20 / 493 * width, this.curPosition.y + i * this.h + this.h / 2)
            c.fillText(content[i], this.curPosition.x + i * this.w + this.w / 2, this.curPosition.y - 15 / 450 * height)
        }
    }
}
</script>

<style scoped>
#irCanvasContainter {
    display: block;
    width: 100%;
    height: 450px;
    border: 1px solid rgb(190, 187, 187);
}

#irCanvas {
    border: 0px solid red;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
