<template>
    <p class="binary_input"> <input type="text" id="wx"> x + <input type="text" id="wy"> y > <input type="text" id="bias">
        <el-button size="mini" @click="mouseClick"> Activate </el-button>
    </p>
    <!-- <p class="binary_input">
        <el-input type="text" id="wx"> </el-input>x +
        <el-input type="text" id="wy"> </el-input> y >
        <el-input type="text" id="bias"> </el-input>
        <button @click="mouseClick"> Activate </button>
    </p> -->
    <!-- <el-input type="text" placeholder="x value"></el-input> -->
    <div id="BinaryContainer">
        <canvas id="binary"></canvas>
        <canvas id="binaryMLP"></canvas>
    </div>

    <p class="img_description"> Figure 1: A Perceptron is a binary classifier.</p>
</template>

<script>
import { onMounted } from 'vue'
import { initialBinary, Activate, binaryMLP } from '@/engine/binary.js'

export default {
    name: 'BinaryGraph',
    setup () {
        onMounted(() => {
            function adjustFigure (containerId, canvasIds, ratio) {
                var binarywidth = document.getElementById(containerId).clientWidth
                var binaryheight = binarywidth * ratio
                console.log(binarywidth, binaryheight)

                document.getElementById(containerId).setAttribute('style', 'height:' + binaryheight + 'px')
                for (var i in canvasIds) {
                    document.getElementById(canvasIds[i]).width = binaryheight
                    document.getElementById(canvasIds[i]).height = binaryheight
                    document.getElementById(canvasIds[i]).setAttribute('style', 'width:' + binaryheight + 'px')
                    document.getElementById(canvasIds[i]).setAttribute('style', 'height:' + binaryheight + 'px')
                }
            }

            // Binary
            adjustFigure('BinaryContainer', ['binary', 'binaryMLP'], 0.491)
            initialBinary()
            binaryMLP()

            window.addEventListener('resize', function () {
                adjustFigure('BinaryContainer', ['binary', 'binaryMLP'], 0.491)
                initialBinary()
                binaryMLP()
            })
            // window.onresize = () => {
            //     return (() => {
            //         console.log('111')
            //         adjustFigure('BinaryContainer', ['binary', 'binaryMLP'], 0.491)
            //         initialBinary()
            //         binaryMLP()
            //     })()
            // }
        })

        function mouseMove (e) {
        }

        function mouseClick () {
            Activate()
            binaryMLP()
        }

        return {
            mouseMove,
            mouseClick,
            onMounted
        }
    }
}
</script>

<style scoped>
p.binary_input, p.n_side_input, p.caption {
    text-align: center;
}

.binary_input .el-button {
    margin-left: 20px;
    margin-bottom: 5px;
}

#wx, #wy, #bias {
    width: 100px;
}

#BinaryContainer{
    display: block;
    width:100%;
}

#binary{
    display: inline;
    border: 2px solid rgb(190, 187, 187);
}

#binaryMLP{
    display: inline;
    border: 1px solid rgb(190, 187, 187);
    margin-left: 1%;
}

p.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
