export class Text {
    constructor (textInfo) {
        this.x = textInfo.x === undefined ? 0 : textInfo.x
        this.y = textInfo.y === undefined ? 0 : textInfo.y
        this.text = textInfo.text === undefined ? 'text' : textInfo.text
        this.font = textInfo.font === undefined ? 'Open Sans' : textInfo.font
        this.size = textInfo.size === undefined ? '18px' : textInfo.size
        this.color = textInfo.color === undefined ? 'black' : textInfo.color
        this.rotate = textInfo.rotate === undefined ? 0 : textInfo.rotate
        this.show = true
    }

    draw (ctx) {
        if (this.show) {
            ctx.save()
            ctx.translate(this.x, this.y)
            ctx.rotate(this.rotate)
            ctx.font = this.size + ' ' + this.font
            ctx.fillStyle = this.color
            ctx.textAlign = 'center'
            ctx.textBaseline = 'middle'
            ctx.fillText(this.text, 0, 0)
            ctx.restore()
        }
    }

    update (ctx) {
        if (this.show) {
            this.draw(ctx)
        }
    }
}
