import { Node, createNodes } from './Node.js'
import { Text } from './Text.js'
import * as util from '@/utils/util.js'
const PI = Math.PI

export class Tree {
    constructor (originX, originY) {
        this.originX = originX
        this.originY = originY
        this.topicDist = 100
        this.lectureDist = 240
        this.rootRadius = 30
        this.topicRadius = 25
        this.lectureRadius = 18
        this.nodesDict = {}
        this.linesArray = []
    };

    init () {
        // Root
        const rootInfo = {
            id: 'root',
            x: this.originX,
            y: this.originY,
            initRadius: 0,
            color: util.wineRed
        }

        const root = new Node(rootInfo)
        this.nodesDict['root'] = root

        /* ---------- Topics (MLP. CNN, RNN, RL) ---------- */
        const topicsInfo = [
            {
                id: 'MLP',
                radian: 3 / 4 * PI,
                initRadius: this.topicRadius,
                dist: this.topicDist,
                color: util.wineRed,
                textInfo: { text: 'MLP', color: util.white, dx: 0, dy: 0 }
            },
            {
                id: 'CNN',
                radian: 1 / 4 * PI,
                initRadius: this.topicRadius,
                dist: this.topicDist,
                color: util.grey,
                textInfo: { text: 'CNN', color: util.white, dx: 0, dy: 0 }
            },
            {
                id: 'RNN',
                radian: -1 / 4 * PI,
                initRadius: this.topicRadius,
                dist: this.topicDist,
                color: util.grey,
                textInfo: { text: 'RNN', color: util.white, dx: 0, dy: 0 }
            },
            {
                id: 'RL',
                radian: -3 / 4 * PI,
                initRadius: this.topicRadius,
                dist: this.topicDist,
                color: util.grey,
                textInfo: { text: 'RL', color: util.white, dx: 0, dy: 0 }
            }
        ]
        createNodes(root, topicsInfo, this.nodesDict, this.linesArray)

        /* ---------- MLP Lectures ---------- */
        const mlpInfo = [
            {
                id: 'Lec 1',
                radian: 10 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 1', color: util.black, dx: 0, dy: -27 },
                lineRadian: [3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 2',
                radian: 9 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.wineRed,
                textInfo: { text: 'Lec 2', color: util.black, dx: 0, dy: -27 },
                lineRadian: [3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 3',
                radian: 8 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 3', color: util.black, dx: 0, dy: -27 },
                lineRadian: [3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 4',
                radian: 7 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.wineRed,
                textInfo: { text: 'Lec 4', color: util.black, dx: 0, dy: -27 },
                lineRadian: [3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 5',
                radian: 6 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 5', color: util.black, dx: 0, dy: -27 },
                lineRadian: [3 / 4 * PI, undefined]
            }
        ]
        createNodes(this.nodesDict['MLP'], mlpInfo, this.nodesDict, this.linesArray)

        /* ---------- CNN Lectures ---------- */
        const cnnInfo = [
            {
                id: 'Lec 6',
                radian: 5 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.wineRed,
                textInfo: { text: 'Lec 6', color: util.black, dx: 0, dy: -27 },
                lineRadian: [1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 7',
                radian: 4 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.wineRed,
                textInfo: { text: 'Lec 7', color: util.black, dx: 0, dy: -27 },
                lineRadian: [1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 8',
                radian: 3 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 8', color: util.black, dx: 0, dy: -27 },
                lineRadian: [1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 9',
                radian: 2 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 9', color: util.black, dx: 0, dy: -27 },
                lineRadian: [1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 10',
                radian: 1 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 10', color: util.black, dx: 0, dy: -27 },
                lineRadian: [1 / 4 * PI, undefined]
            }
        ]
        createNodes(this.nodesDict['CNN'], cnnInfo, this.nodesDict, this.linesArray)

        /* ---------- RNN Lectures ---------- */
        const rnnInfo = [
            {
                id: 'Lec 11',
                radian: -1 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 11', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 12',
                radian: -2 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 12', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 13',
                radian: -3 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 13', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 14',
                radian: -4 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 14', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-1 / 4 * PI, undefined]
            },
            {
                id: 'Lec 15',
                radian: -5 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 15', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-1 / 4 * PI, undefined]
            }
        ]
        createNodes(this.nodesDict['RNN'], rnnInfo, this.nodesDict, this.linesArray)

        // /* ---------- RL Lectures ---------- */
        const rlInfo = [
            {
                id: 'Lec 16',
                radian: -6 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 16', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 17',
                radian: -7 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 17', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 18',
                radian: -8 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 18', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 19',
                radian: -9 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 19', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-3 / 4 * PI, undefined]
            },
            {
                id: 'Lec 20',
                radian: -10 / 11 * PI,
                initRadius: this.lectureRadius,
                dist: this.lectureDist,
                color: util.grey,
                textInfo: { text: 'Lec 20', color: util.black, dx: 0, dy: 30 },
                lineRadian: [-3 / 4 * PI, undefined]
            }
        ]
        createNodes(this.nodesDict['RL'], rlInfo, this.nodesDict, this.linesArray)
    }

    update (ctx, mouseX, mouseY) {
        // Update lines
        for (var i = 0; i < this.linesArray.length; i++) {
            this.linesArray[i].update(ctx)
        }

        // Update nodes
        for (const key in this.nodesDict) {
            this.nodesDict[key].update(ctx, mouseX, mouseY)
        }
    }
}
