import { calculatePosition } from './Utility.js'
import * as util from '@/utils/util.js'
import { Text } from './Text.js'
import { Line } from './Line.js'

export function createNodes (parent, childrenInfo, nodesDict, linesArray = undefined) {
    for (var i = 0; i < childrenInfo.length; i++) {
        // Create new node
        const childInfo = childrenInfo[i]
        const cord = calculatePosition(parent.x, parent.y, childInfo.radian, childInfo.dist)
        childInfo.x = cord[0]
        childInfo.y = cord[1]
        const newNode = new Node(childInfo)
        nodesDict[childInfo.id] = newNode

        // Add text to the node or not
        const textInfo = childInfo['textInfo']
        if (textInfo !== undefined) {
            newNode.setText(new Text(textInfo), textInfo['dx'], textInfo['dy'])
        }

        // Connect line with the node or not
        if (linesArray !== undefined) {
            const parentLineRadian = childInfo['lineRadian'] === undefined ? undefined : childInfo['lineRadian'][0]
            const childLineRadian = childInfo['lineRadian'] === undefined ? undefined : childInfo['lineRadian'][1]
            const newLine = new Line(
                { entity: parent, lineRadian: parentLineRadian }, // parentInfo
                { entity: newNode, lineRadian: childLineRadian }, // childInfo
                childInfo['lineColor'] // line color
            )

            // Set line text if needed
            const lineTextInfo = childInfo['lineTextInfo']
            if (lineTextInfo !== undefined) {
                const newText = new Text({
                    text: lineTextInfo['lineText'],
                    font: lineTextInfo['lineTextFont'],
                    size: lineTextInfo['lineTextSize'],
                    color: lineTextInfo['lineTextColor'],
                    rotate: lineTextInfo['lineTextRotate']
                })
                newLine.setText(newText, lineTextInfo['dx'], lineTextInfo['dy'])
            }

            linesArray.push(newLine)
        }
    }
}

export class Node {
    constructor (nodeInfo) {
        // Node basic properties
        this.id = nodeInfo.id
        this.x = nodeInfo.x
        this.y = nodeInfo.y
        this.color = nodeInfo.color
        this.initRadius = nodeInfo.initRadius // animation related property
        this.curRadius = this.initRadius // animation related property
        this.maxRadius = 1.4 * this.initRadius // animation related property
        this.sensitivity = this.initRadius + 8.2 // animation related property

        // Node text properties
        this.text = undefined
        this.textRadian = undefined

        // Node text properties
        this.lineChildren = {}
    }

    setText (text, dx = 0, dy = 0) {
        // Overwrite the position of text
        this.text = text
        text.x = this.x + dx
        text.y = this.y + dy
        const textRadius = Math.sqrt(dx * dx + dy * dy)

        if (textRadius === 0) {
            this.textSin = 0
            this.textCos = 0
        } else {
            this.textSin = -dy / textRadius
            this.textCos = dx / textRadius
        }
    }

    draw (ctx) {
        // Draw node
        ctx.beginPath()
        ctx.arc(this.x, this.y, this.curRadius, 0, Math.PI * 2, false)
        ctx.fillStyle = this.color
        ctx.stroke()
        ctx.fill()

        // Draw text
        if (this.text !== undefined) {
            this.text.draw(ctx)
        }
    }

    update (ctx, mouseX, mouseY) {
        // if (!util.unpublishedLec.includes(this.id))
        // Interaction
        if (mouseX - this.x > -this.sensitivity && mouseX - this.x < this.sensitivity &&
            mouseY - this.y > -this.sensitivity && mouseY - this.y < this.sensitivity) {
            // Node glows when mouse hovers it
            if (this.curRadius < this.maxRadius) {
                this.curRadius += 3
                // Grows text
                if (this.text !== undefined) {
                    this.text.x += 3 * this.textCos
                    this.text.y -= 3 * this.textSin
                }
            }
        } else if (this.curRadius > this.initRadius) {
            // Node shrink when mouse leaves
            this.curRadius -= 2
            // Shrink text
            if (this.text !== undefined) {
                this.text.x -= 2 * this.textCos
                this.text.y += 2 * this.textSin
            }
        }
        this.draw(ctx)
    }

    // compare two nodes
    // two nodes are equal if id, x and y are same
    equal (node) {
        const properties = ['id', 'x', 'y']
        for (const property of properties) {
            if (node[property] !== this[property]) {
                return false
            }
        }
        return true
    }
}
