<template>
    <div class="wrapper">
        <div
        id="kmCanvasContainter"
        @mousemove="mouseMove"
        >
            <canvas @mousemove="mouseMove" id='kmCanvas' class="karnaugh-map"></canvas>
        </div>
        <div class="groupExplain">
            <div>
                <p v-if="selectedGroup1 === false" style='color:rgba(207, 207, 207, 0.8)'> In <strong>group 1</strong>, we notice that when (Y, Z) = (0, 0), our output is always 1 regardless what values (W, X) are, so
                    we can connect Y and Z to a hidden neuron to represent group 1.
                </p>
                <p v-else-if="selectedGroup1 === true"> In <strong>group 1</strong>, we notice that when (Y, Z) = (0, 0), our output is always 1 regardless what values (W, X) are, so
                    we can connect Y and Z to a hidden neuron to represent group 1.
                </p>
            </div>

            <div>
                <p v-if="selectedGroup2 === false" style='color:rgba(207, 207, 207, 0.8)'>In <strong>group 2</strong>, when (W, X) = (0, 1) and Y = 0, our output is always 1 regardless Z values, so
                    we can connect W, X and Y to a hidden neuron to represent group 2.
                </p>
                <p v-else-if="selectedGroup2 === true"> In <strong>group 2</strong>, when (W, X) = (0, 1) and Y = 0, our output is always 1 regardless Z values, so
                    we can connect W, X and Y to a hidden neuron to represent group 2.
                </p>
            </div>

            <div>
                <p v-if="selectedGroup3 === false" style='color:rgba(207, 207, 207, 0.8)'> <strong>group 3</strong>, when X = 0 and (Y, Z) = (1, 0), our output is always 1 regardless W values, so
                    we can connect X, Y and Z to a hidden neuron to represent group 3.
                </p>
                <p v-else-if="selectedGroup3 === true"> In <strong>group 3</strong>, when X = 0 and (Y, Z) = (1, 0), our output is always 1 regardless W values, so
                    we can connect X, Y and Z to a hidden neuron to represent group 3.
                </p>
            </div>
        </div>
        <div class="group">
            <div>
                <button class='button1' type="button" @click="selectAll" @mouseover="activateBtn1" @mouseleave="deactivateBtn1" :class="{turnRed: hovered1}"> Select All </button>
            </div>
            <div>
                <input type="radio" v-model="selectedGroup" checked value="g1"> Group 1
            </div>
            <div>
                <input type="radio" v-model="selectedGroup" value="g2"> Group 2
            </div>
            <div>
                <input type="radio" v-model="selectedGroup" value="g3"> Group 3
            </div>
            <!-- <div>
                <button class='button2' type="button" @click="resetAll" @mouseover="activateBtn2" @mouseleave="deactivateBtn2" :class="{turnRed: hovered2}"> Reset </button>
            </div> -->
        </div>
    </div>
</template>

<script>
import { ref, onMounted, watch } from 'vue'
import { MLP } from '@/engine/MLP.js'
import * as util from '@/utils/util.js'

export default {
    name: 'KarnaughMap',

    setup () {
        const mouseX = ref()
        const mouseY = ref()
        const selectedGroup = ref('g1')
        const selectedGroup1 = ref(true)
        const selectedGroup2 = ref(true)
        const selectedGroup3 = ref(true)
        const hovered1 = ref()
        const hovered2 = ref()
        var mlp

        function activateBtn1 () {
            hovered1.value = true
        }

        function deactivateBtn1 () {
            hovered1.value = false
        }

        function activateBtn2 () {
            hovered2.value = true
        }

        function deactivateBtn2 () {
            hovered2.value = false
        }

        function selectAll () {
            selectedGroup1.value = true
            selectedGroup2.value = true
            selectedGroup3.value = true
        }

        // function resetAll () {
        //     selectedGroup1.value = false
        //     selectedGroup2.value = false
        //     selectedGroup3.value = false
        // }

        function mouseMove (e) {
            const canvas = document.getElementsByClassName('karnaugh-map')[0]
            var bounds = canvas.getBoundingClientRect()
            mouseX.value = e.pageX - bounds.left - scrollX
            mouseY.value = e.pageY - bounds.top - scrollY
            mouseX.value /= bounds.width
            mouseY.value /= bounds.height
            mouseX.value *= canvas.width
            mouseY.value *= canvas.height
        }

        onMounted(() => {
            const canvasContainer = document.getElementById('kmCanvasContainter')
            const canvas = document.getElementsByClassName('karnaugh-map')[0]
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth
            canvas.height = canvasContainer.offsetHeight

            // karnaugh map drawing
            var mapBlocks

            // MLP drawing
            var nodesDict = {}
            var linesArray = []

            function init (width, height) {
                linesArray = []
                nodesDict = {}
                mapBlocks = new BlockMap(40 / 493 * width, 50 / 400 * height, 70 / 493 * width, 100 / 300 * height)
                const layersInfo = [
                    {
                        numNodes: 4,
                        radius: 10 / 493 * width,
                        color: '#db1c4c',
                        nodeTexts: ['W', 'X', 'Y', 'Z'],
                        textSize: (16 / 493 * width) + 'px'
                    },
                    {
                        numNodes: 3,
                        radius: 10 / 493 * width,
                        color: '#BBBBBB'
                    },
                    {
                        numNodes: 1,
                        radius: 10 / 493 * width,
                        color: '#db1c4c'
                    }
                ]
                const yIntervalArray = []
                for (var i = 0; i < layersInfo.length; i++) {
                    yIntervalArray.push(60 / 300 * height)
                }
                mlp = new MLP(280 / 493 * width, 180 / 300 * height, layersInfo, 90 / 493 * width, yIntervalArray)
                mlp.addToEngine(nodesDict)
                mlp.connectTwoNodes('1-3', 0, '2-1', Math.PI, linesArray)
                mlp.connectTwoNodes('1-4', 0, '2-1', Math.PI, linesArray)
                mlp.connectTwoNodes('2-1', 0, '3-1', Math.PI, linesArray)
                mlp.connectTwoNodes('1-1', 0, '2-2', Math.PI, linesArray)
                mlp.connectTwoNodes('1-2', 0, '2-2', Math.PI, linesArray)
                mlp.connectTwoNodes('1-3', 0, '2-2', Math.PI, linesArray)
                mlp.connectTwoNodes('2-2', 0, '3-1', Math.PI, linesArray)
                mlp.connectTwoNodes('1-2', 0, '2-3', Math.PI, linesArray)
                mlp.connectTwoNodes('1-3', 0, '2-3', Math.PI, linesArray)
                mlp.connectTwoNodes('1-4', 0, '2-3', Math.PI, linesArray)
                mlp.connectTwoNodes('2-3', 0, '3-1', Math.PI, linesArray)
            }

            watch(selectedGroup, (cur, pre) => {
                if (cur === 'g1') {
                    selectedGroup1.value = true
                    selectedGroup2.value = false
                    selectedGroup3.value = false
                } else if (cur === 'g2') {
                    selectedGroup1.value = false
                    selectedGroup2.value = true
                    selectedGroup3.value = false
                } else if (cur === 'g3') {
                    selectedGroup1.value = false
                    selectedGroup2.value = false
                    selectedGroup3.value = true
                }
                console.log(selectedGroup2.value)
            })

            watch(selectedGroup1, (cur, pre) => {
                if (cur === true) {
                    mlp.connectTwoNodes('1-3', 0, '2-1', Math.PI, linesArray)
                    mlp.connectTwoNodes('1-4', 0, '2-1', Math.PI, linesArray)
                    mlp.connectTwoNodes('2-1', 0, '3-1', Math.PI, linesArray)
                } else {
                    linesArray = mlp.disconnectTwoNodes('1-3', '2-1', linesArray)
                    linesArray = mlp.disconnectTwoNodes('1-4', '2-1', linesArray)
                    linesArray = mlp.disconnectTwoNodes('2-1', '3-1', linesArray)
                }
            })

            watch(selectedGroup2, (cur, pre) => {
                if (cur === true) {
                    mlp.connectTwoNodes('1-1', 0, '2-2', Math.PI, linesArray)
                    mlp.connectTwoNodes('1-2', 0, '2-2', Math.PI, linesArray)
                    mlp.connectTwoNodes('1-3', 0, '2-2', Math.PI, linesArray)
                    mlp.connectTwoNodes('2-2', 0, '3-1', Math.PI, linesArray)
                } else {
                    linesArray = mlp.disconnectTwoNodes('1-1', '2-2', linesArray)
                    linesArray = mlp.disconnectTwoNodes('1-2', '2-2', linesArray)
                    linesArray = mlp.disconnectTwoNodes('1-3', '2-2', linesArray)
                    linesArray = mlp.disconnectTwoNodes('2-2', '3-1', linesArray)
                }
            })

            watch(selectedGroup3, (cur, pre) => {
                if (cur === true) {
                    mlp.connectTwoNodes('1-2', 0, '2-3', Math.PI, linesArray)
                    mlp.connectTwoNodes('1-3', 0, '2-3', Math.PI, linesArray)
                    mlp.connectTwoNodes('1-4', 0, '2-3', Math.PI, linesArray)
                    mlp.connectTwoNodes('2-3', 0, '3-1', Math.PI, linesArray)
                } else {
                    linesArray = mlp.disconnectTwoNodes('1-2', '2-3', linesArray)
                    linesArray = mlp.disconnectTwoNodes('1-3', '2-3', linesArray)
                    linesArray = mlp.disconnectTwoNodes('1-4', '2-3', linesArray)
                    linesArray = mlp.disconnectTwoNodes('2-3', '3-1', linesArray)
                }
            })

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                init(canvas.width, canvas.height)
                mlp.addToEngine(nodesDict)
            })

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, canvas.width, canvas.height)

                // Update map drawing
                mapBlocks.draw(ctx, selectedGroup1.value, selectedGroup2.value, selectedGroup3.value, canvas.width, canvas.height)

                // Update mlp
                for (var i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }

                // Update nodes
                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX.value, mouseY.value)
                }
            }
            init(canvas.width, canvas.height)
            animate()
        })
        return {
            onMounted,
            mouseX,
            mouseY,
            mouseMove,
            selectedGroup,
            selectedGroup1,
            selectedGroup2,
            selectedGroup3,
            selectAll,
            // resetAll,
            hovered1,
            activateBtn1,
            deactivateBtn1,
            hovered2,
            activateBtn2,
            deactivateBtn2
        }
    }
}

class BlockMap {
    constructor (w, h, x, y) {
        var arr = []
        for (var i = 0; i < 16; i++) {
            arr.push('')
        }
        this.contentArray = arr
        this.rows = 4
        this.cols = 4
        this.w = w // 40
        this.h = h // 40
        this.curPosition = {
            x: x, // 100,
            y: y // 100
        }
        this.startPosition = this.curPosition
        this.color = util.grey
        this.g1Box = {
            x: this.startPosition.x,
            y: this.startPosition.y,
            w: this.w,
            h: this.h * this.rows
        }
        this.g2Box = {
            x: this.startPosition.x,
            y: this.startPosition.y + this.h,
            w: this.w * 2,
            h: this.h
        }
        this.g3Box1 = {
            x: this.startPosition.x + this.w * (this.cols - 1),
            y: this.startPosition.y,
            w: this.w,
            h: this.h
        }
        this.g3Box2 = {
            x: this.startPosition.x + this.w * (this.cols - 1),
            y: this.startPosition.y + this.h * (this.rows - 1),
            w: this.w,
            h: this.h
        }
    }

    draw (c, selectedGroup1, selectedGroup2, selectedGroup3, width, height) {
        // draw block
        var grows = {
            g1: [0, 1, 2, 3],
            g2: [1, 1],
            g3: [0, 3]
        }
        var gcols = {
            g1: [0, 0, 0, 0],
            g2: [0, 1],
            g3: [3, 3]
        }
        if (selectedGroup1 === false) {
            grows.g1 = []
            gcols.g1 = []
        }

        if (selectedGroup2 === false) {
            grows.g2 = []
            gcols.g2 = []
        }

        if (selectedGroup3 === false) {
            grows.g3 = []
            gcols.g3 = []
        }

        var hrows = []
        var hcols = []
        const keysgrows = Object.keys(grows)
        const keysgcols = Object.keys(gcols)
        for (const key of keysgrows) {
            hrows = hrows.concat(grows[key])
        }
        for (const key of keysgcols) {
            hcols = hcols.concat(gcols[key])
        }

        for (let i = 0; i < this.rows; i++) {
            for (let j = 0; j < this.cols; j++) {
                c.fillStyle = this.color
                c.fillRect(this.curPosition.x + i * this.w, this.curPosition.y + j * this.h, this.w, this.h)
            }
        }
        for (let i = 0; i < hrows.length; i++) {
            for (let j = 0; j < hcols.length; j++) {
                c.fillStyle = util.wineRed
                c.fillRect(this.curPosition.x + hcols[i] * this.w, this.curPosition.y + hrows[i] * this.h, this.w, this.h)
            }
        }

        // draw row lines
        c.lineWidth = 1
        for (let i = 0; i < this.rows + 1; i++) {
            c.beginPath()
            c.moveTo(this.startPosition.x, this.startPosition.y + i * this.h)
            c.lineTo(this.startPosition.x + this.w * this.cols, this.startPosition.y + i * this.h)
            c.strokeStyle = 'white'
            c.stroke()
        }
        // draw col lines
        for (let i = 0; i < this.cols + 1; i++) {
            c.beginPath()
            c.moveTo(this.startPosition.x + i * this.w, this.startPosition.y)
            c.lineTo(this.startPosition.x + i * this.w, this.startPosition.y + this.h * this.rows)
            c.strokeStyle = 'white'
            c.stroke()
        }

        // draw highlt box
        for (const key of keysgrows) {
            if (grows['g1'].length !== 0) {
                c.strokeStyle = 'yellow'
                c.lineWidth = 4
                c.strokeRect(this.g1Box.x, this.g1Box.y, this.g1Box.w, this.g1Box.h)
            }
            if (grows['g2'].length !== 0) {
                c.strokeStyle = 'blue'
                c.lineWidth = 4
                c.strokeRect(this.g2Box.x, this.g2Box.y, this.g2Box.w, this.g2Box.h)
            }
            if (grows['g3'].length !== 0) {
                c.strokeStyle = 'green'
                c.lineWidth = 4
                c.strokeRect(this.g3Box1.x, this.g3Box1.y, this.g3Box1.w, this.g3Box1.h)
                c.strokeRect(this.g3Box2.x, this.g3Box2.y, this.g3Box2.w, this.g3Box2.h)
            }
        }

        // add text
        c.beginPath()
        c.moveTo(this.startPosition.x, this.startPosition.y)
        c.lineTo(this.startPosition.x - 50 / 493 * width, this.startPosition.y - 50 / 300 * height)
        c.strokeStyle = 'black'
        c.lineWidth = 1
        c.stroke()

        c.font = '15px Sans-serif'
        c.fillStyle = 'black'
        c.fillText('WX', this.curPosition.x - 50 / 493 * width, this.curPosition.y - 50 / 300 * height + 30 / 300 * height)
        c.fillText('YZ', this.curPosition.x - 50 / 493 * width + 30 / 493 * width, this.curPosition.y - 40 / 300 * height)

        const content = ['00', '01', '11', '10']
        c.textAlign = 'center'
        c.textBaseline = 'middle'
        c.font = (this.h / 2).toString() + 'px Sans-serif'
        for (let i = 0; i < content.length; i++) {
            c.fillText(content[i], this.curPosition.x - 20 / 493 * width, this.curPosition.y + i * this.h + this.h / 2)
            c.fillText(content[i], this.curPosition.x + i * this.w + this.w / 2, this.curPosition.y - 15 / 300 * height)
        }
    }
}

</script>

<style scoped>
.wrapper {
    width: 100%;
    height: auto;
    border: 1px solid black
}

#kmCanvasContainter {
    display: block;
    width: 100%;
    height: 300px;
    border: 0px solid blue;
}

#kmCanvas {
    border: 0px solid red;
}

.group {
    display: flex;
    margin-top: 10px;
    border: 0px solid red;
    justify-content: center;
    margin-bottom: 4%;
}

.group div {
    margin: 5px 13px;
}

.groupExplain {
    margin-top: 4%;
    border: 0px solid black;
    height: auto;
}

.groupExplain div {
    padding-bottom: 4%;
    padding-left: 5%;
    padding-right: 5%;
}

button {
    color: black;
    padding: 3px 5px;
    font-size: 15px;
    border-radius: 7px;
    border: 0px solid black;
    background-color: #ECECEC;
    outline: none;
}

.turnRed {
    background-color: #F4D9D9;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
