import { DrawMLP } from './binary.js'

export function initialPolygon () {
    var initial = document.getElementById('polygon')
    var initalC = initial.getContext('2d')
    var w = document.getElementById('polygon').offsetWidth
    var h = document.getElementById('polygon').offsetHeight

    initalC.clearRect(0, 0, w, h)
    initalC.rect(0, 0, w, h)
    initalC.fillStyle = 'yellow'
    initalC.globalAlpha = 0.6
    initalC.fill()

    initalC.beginPath()
    initalC.moveTo(0, h / 2)
    initalC.lineTo(w, h / 2)
    initalC.moveTo(w / 2, 0)
    initalC.lineTo(w / 2, h)
    initalC.strokeStyle = 'black'
    initalC.lineWidth = 2
    initalC.globalAlpha = 1.0
    initalC.closePath()
    initalC.stroke()

    initalC.save()
    initalC.beginPath()
    initalC.setLineDash([5, 15])
    initalC.lineWidth = 2
    initalC.strokeStyle = 'black'
    initalC.moveTo(w / 2 + Math.min(h, w) * 0.9 / 2, h / 2)
    initalC.arc(w / 2, h / 2, Math.min(h, w) * 0.9 / 2, 0, 2 * Math.PI)
    initalC.stroke()
    initalC.fillStyle = 'red'
    initalC.globalAlpha = 0.6
    initalC.fill()
    initalC.restore()
}

export function DrawPolygon () {
    var corners = document.getElementById('nside').value
    var c = document.getElementById('polygon').getContext('2d')
    var w = document.getElementById('polygon').offsetWidth
    var h = document.getElementById('polygon').offsetHeight
    var width = w * 0.9
    var height = h * 0.9

    initialPolygon()
    // DrawMLP
    var MLPc = document.getElementById('polygonMLP').getContext('2d')
    var MLPw = document.getElementById('polygonMLP').offsetWidth
    var MLPh = document.getElementById('polygonMLP').offsetHeight
    MLPc.clearRect(0, 0, MLPw, MLPh)

    if (isNaN(corners) || corners < 3) { initialPolygon(); return }
    corners = parseInt(corners)
    corners = Math.min(corners, 35)

    var angle = (Math.PI * 2) / corners
    var radius = Math.min(width, height) / 2

    var midpoint = [w / 2, h / 2]
    var points = [{ x: midpoint[0], y: midpoint[1] - radius }]

    for (var i = 1; i < corners; i++) {
        points.push({
            x: Math.sin(angle * i) * radius + midpoint[0],
            y: -Math.cos(angle * i) * radius + midpoint[1]
        })
    }

    initialPolygon()
    c.beginPath()
    c.moveTo(points[0].x, points[0].y)
    for (i = 1; i < points.length; i++) { c.lineTo(points[i].x, points[i].y) }
    c.closePath()
    c.strokeStyle = 'black'
    c.lineWidth = 3
    c.globalAlpha = 1.0
    c.stroke()
    c.fillStyle = 'blue'
    c.globalAlpha = 0.6
    c.fill()

    // Notation
    MLPc.font = w * 0.05 + 'px Arial'
    MLPc.fillStyle = 'black'
    MLPc.textAlign = 'right'
    MLPc.fillText('input x', w * 17 / 100, h * 35 / 100)
    MLPc.fillText('input y', w * 17 / 100, h * 68 / 100)
    MLPc.fillText('output', w * 97 / 100, h * 51.5 / 100)
    MLPc.fillText('AND', w * 75 / 100, h * 57 / 100)
    DrawMLP('polygonMLP', [2, corners, 1, 1])
}

export function Polygon (corners, elementid) {
    var window = document.getElementById(elementid)
    var c = window.getContext('2d')
    var w = document.getElementById(elementid).offsetWidth
    var h = document.getElementById(elementid).offsetHeight
    var width = w * 0.9
    var height = h * 0.9

    c.clearRect(0, 0, w, h)
    c.rect(0, 0, w, h)
    c.fillStyle = 'yellow'
    c.globalAlpha = 0.6
    c.fill()

    c.beginPath()
    c.moveTo(0, h / 2)
    c.lineTo(w, h / 2)
    c.moveTo(w / 2, 0)
    c.lineTo(w / 2, h)
    c.strokeStyle = 'black'
    c.lineWidth = 3
    c.globalAlpha = 1.0
    c.closePath()
    c.stroke()

    var angle = (Math.PI * 2) / corners
    var radius = Math.min(width, height) / 2

    var midpoint = [w / 2, h / 2]
    var points = [{ x: midpoint[0], y: midpoint[1] - radius }]

    for (var i = 1; i < corners; i++) {
        points.push({
            x: Math.sin(angle * i) * radius + midpoint[0],
            y: -Math.cos(angle * i) * radius + midpoint[1]
        })
    }

    c.beginPath()
    c.moveTo(points[0].x, points[0].y)
    for (i = 1; i < points.length; i++) { c.lineTo(points[i].x, points[i].y) }
    c.closePath()
    c.strokeStyle = 'black'
    c.lineWidth = 3
    c.globalAlpha = 1.0
    c.stroke()
    c.fillStyle = 'blue'
    c.globalAlpha = 0.6
    c.fill()
}
