export function getNodePosition (nodeId) {
    const nodeIdNumber = nodeId.replace('x', '')
    const info = { layerNumber: nodeIdNumber.split('')[0], nodeIndex: nodeIdNumber.split('')[1] }
    return info
}

export function getWeights () {
    var weights = require('./weights.json')
    return weights
}

export function getPreds () {
    var preds = require('./predicts.json')
    return preds
}
