export class Line {
    constructor (parentInfo, childInfo, color = undefined, textInfo = undefined) {
        /**
         * parentInfo: { entity: parent, lineRadian: parentLineRadian }
         * childInfo:  { entity: child, lineRadian: childLineRadian }
         * color = 'black'
         * textInfo { entity: new Text(...), dx: 0, dy: 0 }
         */
        this.parent = parentInfo['entity']
        this.child = childInfo['entity']
        this.parentLineRadian = parentInfo['lineRadian']
        this.childLineRadian = childInfo['lineRadian']
        this.color = color === undefined ? 'black' : color

        // Text related properties
        this.text = textInfo === undefined ? undefined : textInfo['entity']
        this.textDX = textInfo === undefined ? undefined : textInfo['dx']
        this.textDY = textInfo === undefined ? undefined : textInfo['dy']
    };

    draw (ctx) {
        // Start drawing line
        ctx.beginPath()

        /**
         * (1) If line radian is NOT undefined, connect the line with the circle surface (this enables line moving animation)
         * (2) Otherwise, connect the line with the circle center (disable line moving aniamtion)
         */
        if (this.parentLineRadian !== undefined) {
            this.parentStartX = this.parent.x + this.parent.curRadius * Math.cos(this.parentLineRadian)
            this.parentStartY = this.parent.y - this.parent.curRadius * Math.sin(this.parentLineRadian)
            ctx.moveTo(this.parentStartX, this.parentStartY)
        } else {
            this.parentStartX = this.parent.x
            this.parentStartY = this.parent.y
            ctx.moveTo(this.parent.x, this.parent.y)
        }

        if (this.childLineRadian !== undefined) {
            this.childStartX = this.child.x + this.child.curRadius * Math.cos(this.childLineRadian)
            this.childStartY = this.child.y - this.child.curRadius * Math.sin(this.childLineRadian)
            ctx.lineTo(this.childStartX, this.childStartY)
        } else {
            this.childStartX = this.child.x
            this.childStartY = this.child.y
            ctx.lineTo(this.child.x, this.child.y)
        }
        ctx.strokeStyle = this.color
        ctx.stroke()
    }

    setText (text, dx = 0, dy = 0) {
        // Overwrite the original text
        this.text = text
        this.textDX = dx
        this.textDY = dy
    }

    update (ctx) {
        this.draw(ctx)
        // Draw text
        if (this.text !== undefined) {
            this.text.x = (this.parentStartX + this.childStartX) / 2 + this.textDX
            this.text.y = (this.parentStartY + this.childStartY) / 2 + this.textDY
            this.text.draw(ctx)
        }
    }
}
