/* eslint-disable no-unused-vars */
<template>
    <div id="noteSidebar">
        <transition
            enter-active-class='animate__animated animate__fadeInRight'
            leave-active-class='animate__animated animate__fadeOutRight'
        >
            <div class="note" v-if="showNote">
                <div class='noteCurtain'> </div>

                <button @click="hideNote" class='rightbackContainer'>
                    <img src='@/assets/rightback.png' class='rightback'>
                </button>

                <div class="noteContainer" id="noteContainer">
                    <div class="noteContent">
                        <Lec2 v-if="lecName==='Lec 2'" />
                        <Lec4 v-else-if="lecName==='Lec 4'" />
                        <Lec6 v-else-if="lecName==='Lec 6'" />
                        <Lec7 v-else-if="lecName==='Lec 7'" />
                    </div>
                </div>
            </div>
        </transition>

        <transition
            enter-active-class='animate__animated animate__fadeInLeft'
            leave-active-class='animate__animated animate__fadeOutLeft'
        >
            <div class="sidebar" v-if="showNote">
                <div class='sidebarCurtain'> </div>
                <div class="sidebarContainer">
                    <div class="sidebarContent">
                        <Side1 v-if="lecName==='Lec 1'"/>
                        <Side2 v-else-if="lecName==='Lec 2'" />
                        <Side3 v-else-if="lecName==='Lec 3'" />
                        <Side4 v-else-if="lecName==='Lec 4'" />
                        <Side6 v-else-if="lecName==='Lec 6'" />
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import { useStore } from 'vuex'
import { toRefs } from 'vue'
import Lec2 from '@/notes/Lec2/Lec2.vue'
import Lec4 from '@/notes/Lec4/Lec4.vue'
import Lec6 from '@/notes/Lec6/Lec6.vue'
import Lec7 from '@/notes/Lec7/Lec7.vue'

import Side1 from '@/sidebar/Lec1/Lec1.vue'
import Side2 from '@/sidebar/Lec2/Lec2.vue'
import Side3 from '@/sidebar/Lec3/Lec3.vue'
import Side4 from '@/sidebar/Lec4/Lec4.vue'
import Side6 from '@/sidebar/Lec6/Lec6.vue'

export default {
    name: 'NoteSidebar',
    components: {
        Lec2,
        Lec4,
        Lec6,
        Lec7,
        Side1,
        Side2,
        Side3,
        Side4,
        Side6
    },
    setup () {
        const store = useStore()
        const { showNote, noteContent, lecName } = toRefs(store.state)
        const hideNote = () => {
            store.commit('hideNote')
        }
        return {
            showNote,
            hideNote,
            lecName,
            noteContent
        }
    }
}
</script>

