        to right bottom,
        rgba(255, 255, 255, 0.97),
        rgba(255, 255, 255, 0.9)
    );
    border-radius: 0%;
    backdrop-filter: blur(10px);
    z-index: 1;
    /* border: 1px solid rgba(190, 178, 178, 0.562); */
    overflow-y: scroll;
}

.noteContent {
