/* eslint-disable no-unused-vars */
/* eslint-disable camelcase */

import { Node } from './Node.js'
import { Text } from './Text.js'

export class Layer {
    constructor (originX, originY, nodesInfoArray, yInterval) {
        this.originX = originX
        this.originY = originY
        this.numNodes = nodesInfoArray.length
        this.yInterval = yInterval
        this.nodes = []

        const topNodeY = originY - (this.numNodes - 1) / 2 * yInterval
        for (var i = 0; i < this.numNodes; i++) {
            const nodeInfo = nodesInfoArray[i]
            nodeInfo['x'] = this.originX
            nodeInfo['y'] = topNodeY + i * this.yInterval
            const newNode = new Node(nodeInfo)
            if (nodeInfo['text'] !== undefined) {
                newNode.setText(new Text({ text: nodeInfo['text'], size: nodeInfo['textSize'] }), 0, -22)
            }
            this.nodes.push(newNode)
        }
    };

    addToEngine (nodesDict) {
        for (var i = 0; i < this.nodes.length; i++) {
            const node = this.nodes[i]
            nodesDict[node.id] = node
        }
    }
}
