/* eslint-disable indent */
import { Layer } from './Layer.js'
import { Line } from './Line.js'
import * as util from '@/utils/util.js'
const PI = Math.PI

export class MLP {
    constructor (originX, originY, layersInfo, xInterval, yIntervalArray) {
        this.originX = originX
        this.originY = originY
        this.xInterval = xInterval
        this.yIntervalArray = yIntervalArray
        this.layers = []
        this.numLayers = layersInfo.length

        // let nodeId = '11'
        for (var i = 0; i < this.numLayers; i++) {
            const layerInfo = layersInfo[i]
            const numNodes = layerInfo['numNodes']
            const nodeTexts = layerInfo['nodeTexts']
            const nodesRadius = layerInfo['radius']
            const nodesColor = layerInfo['color']
            const textSize = layerInfo['textSize']
            const nodesInfoArray = [] // an array of dictionaries

            for (let j = 0; j < numNodes; j++) {
                const nodeInfo = {
                    id: 'x' + (i + 1) + '' + (j + 1),
                    initRadius: nodesRadius,
                    color: nodesColor,
                    text: nodeTexts !== undefined ? nodeTexts[j] : undefined,
                    textSize: textSize !== undefined ? textSize : undefined
                }
                nodesInfoArray.push(nodeInfo)
                // nodeId = (i + 1) + '' + (j + 1)
            }

            this.layers.push(new Layer(originX + i * xInterval, originY, nodesInfoArray, yIntervalArray[i]))
        }
    };

    connectTwoNodes (leftStr, leftLineRadian, rightStr, rightLineRadian, linesArray) {
        const leftLayerIndex = leftStr.split('-')[0] - 1
        const leftNodeIndex = leftStr.split('-')[1] - 1
        const rightLayerIndex = rightStr.split('-')[0] - 1
        const rightNodeIndex = rightStr.split('-')[1] - 1
        const left = this.layers[leftLayerIndex].nodes[leftNodeIndex]
        const right = this.layers[rightLayerIndex].nodes[rightNodeIndex]
        linesArray.push(
            new Line(
                { entity: left, lineRadian: leftLineRadian },
                { entity: right, lineRadian: rightLineRadian },
                util.grey
            )
        )
    }

    connectNodeLayer (nodeStr, nodeLineRadian, layerStr, layerNodeLineRadian, linesArray) {
        const nodeLayerIndex = nodeStr.split('-')[0] - 1
        const nodeIndex = nodeStr.split('-')[1] - 1
        const node = this.layers[nodeLayerIndex].nodes[nodeIndex]
        const layer = this.layers[parseInt(layerStr) - 1]

        for (let i = 0; i < layer.numNodes; i++) {
            var layerNode = layer.nodes[i]
            linesArray.push(
                new Line(
                    { entity: node, lineRadian: layerNodeLineRadian },
                    { entity: layerNode, lineRadian: nodeLineRadian },
                    util.grey
                )
            )
        }
    }

    connectTwoLayers (leftLayerStr, rightLayerStr, linesArray) {
        const leftLayer = this.layers[parseInt(leftLayerStr) - 1]  // 1
        const rightLayer = this.layers[parseInt(rightLayerStr) - 1]  // 2
        for (let i = 0; i < rightLayer.numNodes; i++) {
            const rightLayerNode = rightLayer.nodes[i]

            for (let j = 0; j < leftLayer.numNodes; j++) {
                var leftLayerNode = leftLayer.nodes[j]
                const newLine = new Line(
                    { entity: rightLayerNode, lineRadian: PI }, // right node
                    { entity: leftLayerNode, lineRadian: 0 }, // left node
                    util.grey
                )
                linesArray.push(newLine)
                rightLayerNode.lineChildren[leftLayerStr + '' + (j + 1) + '-' + rightLayerStr + (i + 1)] = newLine
                leftLayerNode.lineChildren[leftLayerStr + '' + (j + 1) + '-' + rightLayerStr + (i + 1)] = newLine
            }
        }
    }

    connectAll (linesArray) {
        for (let i = this.numLayers; i > 0; i--) {
            if (i !== 1) {
                this.connectTwoLayers(i - 1, i, linesArray)
            }
        }
    }

    disconnectTwoNodes (childStr, parentStr, linesArray) {
        const rightLayerIndex = parentStr.split('-')[0] - 1
        const rightNodeIndex = parentStr.split('-')[1] - 1
        const leftLayerIndex = childStr.split('-')[0] - 1
        const leftNodeIndex = childStr.split('-')[1] - 1
        const right = this.layers[rightLayerIndex].nodes[rightNodeIndex]
        const left = this.layers[leftLayerIndex].nodes[leftNodeIndex]

        linesArray = linesArray.filter(function (obj) {
            if (!obj.parent.equal(left)) {
                return true
            }
            if (!obj.child.equal(right)) {
                return true
            }
            return false
        })
        return linesArray
    }

    addToEngine (nodesDict) {
        for (let i = 0; i < this.layers.length; i++) {
            this.layers[i].addToEngine(nodesDict)
        }
    }
}
