<template>
    <div class="wrapper">
        <div
            id="tbCanvasContainter"
            @mousemove="mouseMove"
            @click="mouseClick"
        >
            <canvas id='tbCanvas' class="truth-table"></canvas>
        </div>
        <div class="noteExplain">
            <div class="row-title" v-if="selectedBlock === 'title' ">
                <p>
                Please move your mouse over each row of the truth table to see the changes in neural network!
                </p>
            </div>
            <div class="row1" v-if="selectedBlock === '1' ">
                <p>
                    In the <strong>1<sup>st</sup></strong> row, since the weights of W, X, Y, Z are all negative, the output is <strong>1</strong>
                    if and only if all four inputs are 0. If any of them is not <b>0</b>, the weighted sum would be smaller than 0, and the gate will not fire.
                </p>
            </div>

            <div class="row2" v-if="selectedBlock === '2' ">
                 <p>
                    In the <strong>2<sup>nd</sup></strong> row, since the weights of W, X, Y, Z are (-1,1,-1,-1), the output is <strong>1</strong>
                    if and only if four inputs are (0, 1, 0, 0). If any of them is not the assigned value, the weighted sum would be smaller than 1, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>

            <div class="row3" v-if="selectedBlock === '3' ">
                <p>
                    In the <strong>3<sup>rd</sup></strong> row, since the weights of W, X, Y, Z are (1,1,-1,-1), the output is <strong>1</strong>
                    if and only if four inputs are (1, 1, 0, 0). If any of them is not the assigned value, the weighted sum would be smaller than 2, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>

            <div class="row4" v-if="selectedBlock === '4' ">
                <p>
                    In the <strong>4<sup>th</sup></strong> row, since the weights of W, X, Y, Z are (1,-1,-1,-1), the output is <strong>1</strong>
                    if and only if four inputs are (1, 0, 0, 0). If any of them is not the assigned value, the weighted sum would be smaller than 1, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>

            <div class="row5" v-if="selectedBlock === '5' ">
                <p>
                    In the <strong>5<sup>th</sup></strong> row, since the weights of W, X, Y, Z are (-1,1,-1,1), the output is <strong>1</strong>
                    if and only if four inputs are (0, 1, 0, 1). If any of them is not the assigned value, the weighted sum would be smaller than 2, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>

            <div class="row6" v-if="selectedBlock === '6' ">
                <p>
                    In the <strong>6<sup>th</sup></strong> row, since the weights of W, X, Y, Z are (-1,-1,1,-1), the output is <strong>1</strong>
                    if and only if four inputs are (0, 0, 1, 0). If any of them is not the assigned value, the weighted sum would be smaller than 1, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>

            <div class="row7" v-if="selectedBlock === '7' ">
                <p>
                    In the <strong>7<sup>th</sup></strong> row, since the weights of W, X, Y, Z are (1,-1,1,-1), the output is <strong>1</strong>
                    if and only if four inputs are (1, 0, 1, 0). If any of them is not the assigned value, the weighted sum would be smaller than 2, and the gate will not fire. Note that the inputs are binary, which means that they can only be 0 or 1.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { MLP } from '@/engine/MLP.js'
import * as util from '@/utils/util.js'
import { Text } from '@/engine/Text.js'

export default {
    name: 'TruthTable',

    setup () {
        const mouseX = ref()
        const mouseY = ref()
        const selectedBlock = ref('title')
        var nodesDict = {}
        var linesArray = []
        var mlp
        var blocks
        const weights = {
            1: ['-1', '-1', '-1', '-1', '1'],
            2: ['-1', '1', '-1', '-1', '1'],
            3: ['1', '1', '-1', '-1', '1'],
            4: ['1', '-1', '-1', '-1', '1'],
            5: ['-1', '1', '-1', '1', '1'],
            6: ['-1', '-1', '1', '-1', '1'],
            7: ['1', '-1', '1', '-1', '1']
        }
        const threshHold = ['0', '1', '2', '1', '2', '1', '2']

        function mouseMove (e) {
            const canvas = document.getElementById('tbCanvas')
            var bounds = canvas.getBoundingClientRect()
            mouseX.value = e.pageX - bounds.left - scrollX
            mouseY.value = e.pageY - bounds.top - scrollY
            mouseX.value /= bounds.width
            mouseY.value /= bounds.height
            mouseX.value *= canvas.width
            mouseY.value *= canvas.height
        }

        function init (width, height) {
            nodesDict = {}
            linesArray = []
            const w = 40 / 493 * width
            const h = 25 / 450 * height
            blocks = [
                new Table(['Output', 'W', 'X', 'Y', 'Z'], 1, 5, w, h, { x: 20 / 493 * width, y: 110 / 450 * height }, '#CFCFCF', 'title'),
                new Table([1, 0, 0, 0, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 139 / 450 * height }, '#CFCFCF', '1'),
                new Table([1, 0, 1, 0, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 167 / 450 * height }, '#CFCFCF', '2'),
                new Table([1, 1, 1, 0, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 195 / 450 * height }, '#CFCFCF', '3'),
                new Table([1, 1, 0, 0, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 223 / 450 * height }, '#CFCFCF', '4'),
                new Table([1, 0, 1, 0, 1], 1, 5, w, h, { x: 20 / 493 * width, y: 251 / 450 * height }, '#CFCFCF', '5'),
                new Table([1, 0, 0, 1, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 279 / 450 * height }, '#CFCFCF', '6'),
                new Table([1, 1, 0, 1, 0], 1, 5, w, h, { x: 20 / 493 * width, y: 307 / 450 * height }, '#CFCFCF', '7')
            ]

            // MLP drawing
            const layersInfo = [
                {
                    numNodes: 4,
                    radius: 9 / 493 * width,
                    color: util.wineRed,
                    nodeTexts: ['W', 'X', 'Y', 'Z'],
                    textSize: (13 / 493 * width) + 'px'
                },
                {
                    numNodes: 7,
                    radius: 9 / 493 * width,
                    color: util.grey
                },
                {
                    numNodes: 1,
                    radius: 9 / 493 * width,
                    color: util.wineRed
                }
            ]
            const yIntervalArray = []
            for (var i = 0; i < layersInfo.length; i++) {
                yIntervalArray.push(50 / 450 * height)
            }
            mlp = new MLP(width / 2, height / 2, layersInfo, 110 / 493 * width, yIntervalArray)
        }

        onMounted(() => {
            const canvasContainer = document.getElementById('tbCanvasContainter')
            const canvas = document.getElementById('tbCanvas')
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth // 493
            canvas.height = canvasContainer.offsetHeight
            init(canvas.width, canvas.height)
            mlp.addToEngine(nodesDict)

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                init(canvas.width, canvas.height)
                mlp.addToEngine(nodesDict)
            })

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, canvas.width, canvas.height)

                // Update blocks
                for (let i = 0; i < blocks.length; i++) {
                    blocks[i].update(ctx, mouseX.value, mouseY.value)
                    if (blocks[i].highlightName !== undefined) {
                        selectedBlock.value = blocks[i].highlightName
                    }
                }
                ctx.lineWidth = 1
                // Update nodeDict and linesArray when table row is selected
                if (selectedBlock.value === undefined) {
                    linesArray = []
                } else if (selectedBlock.value === 'title') {
                    linesArray = []
                    nodesDict['x21'].text = undefined
                    nodesDict['x22'].text = undefined
                    nodesDict['x23'].text = undefined
                    nodesDict['x24'].text = undefined
                    nodesDict['x25'].text = undefined
                    nodesDict['x26'].text = undefined
                    nodesDict['x27'].text = undefined
                    nodesDict['x31'].text = undefined
                    mlp.connectAll(linesArray)
                } else {
                    nodesDict['x21'].text = undefined
                    nodesDict['x22'].text = undefined
                    nodesDict['x23'].text = undefined
                    nodesDict['x24'].text = undefined
                    nodesDict['x25'].text = undefined
                    nodesDict['x26'].text = undefined
                    nodesDict['x27'].text = undefined
                    nodesDict['x31'].text = undefined
                    linesArray = []
                    mlp.connectNodeLayer('2-'.concat(selectedBlock.value), 0, '1', Math.PI, linesArray)
                    mlp.connectNodeLayer('2-'.concat(selectedBlock.value), Math.PI, '3', 0, linesArray)
                    for (let i = 0; i < 5; i++) {
                        var textInfo = {
                            text: weights[selectedBlock.value][i],
                            size: 'bold ' + 14 / 493 * canvas.width + 'px',
                            color: util.wineRed
                        }
                        linesArray[i].setText(new Text(textInfo), 0, -5)
                    }
                    var threshSize = 14 / 493 * canvas.width + 'px'
                    nodesDict['x2' + selectedBlock.value].setText(new Text({ text: threshHold[parseInt(selectedBlock.value) - 1], size: threshSize }))
                }

                // Update MLP lines
                for (var i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }

                // Update nodes
                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX.value, mouseY.value)
                }
            }
            animate()
        })
        return {
            onMounted,
            mouseX,
            mouseY,
            mouseMove,
            selectedBlock
        }
    }
}

class Table {
    constructor (contentArray, rows, cols, w, h, startPosition, color, name) {
        this.contentArray = contentArray
        this.rows = rows
        this.cols = cols
        this.w = w
        this.h = h
        this.startPosition = startPosition
        this.curPosition = startPosition
        this.color = color
        this.highlight = 'black'
        this.lineSize = 1
        this.name = name
        this.highlightName = undefined
    };

    draw (c) {
        // draw block
        c.fillStyle = this.color
        c.lineWidth = this.lineSize
        c.strokeStyle = this.highlight
        c.strokeRect(this.curPosition.x, this.curPosition.y, this.w * this.cols, this.h * this.rows)
        c.fillRect(this.curPosition.x, this.curPosition.y, this.w * this.cols, this.h * this.rows)

        // add text
        for (let i = 0; i < this.rows; i++) {
            for (let j = 0; j < this.cols; j++) {
                c.textAlign = 'center'
                c.textBaseline = 'middle'
                c.font = (this.h / 2).toString() + 'px Sans-serif'
                c.fillStyle = 'black'
                c.fillText(this.contentArray[i * this.cols + j], this.curPosition.x + j * this.w + this.w / 2, this.curPosition.y + i * this.h + this.h / 2)
            }
        }
    }

    update (c, mouseX, mouseY) {
        // add mousemove interaction
        if (mouseX > this.curPosition.x && mouseX < this.curPosition.x + this.w * this.cols &&
            mouseY < this.curPosition.y + this.h * this.rows && mouseY > this.curPosition.y) {
            this.highlight = 'red'
            this.lineSize = 3
            this.highlightName = this.name
        } else if (this.highlight === 'red') {
            this.highlight = 'black'
            this.lineSize = 1
            this.highlightName = undefined
        }
        this.draw(c)
    }
}

</script>

<style scoped>
.wrapper {
    width: 100%;
    height: auto;
    border: 1px solid rgb(190, 187, 187);
}

#tbCanvasContainter {
    display: block;
    width: 100%;
    height: 450px;
    border: 0px solid blue;
}

#tbCanvas {
    border: 0px solid red;
}

.noteExplain div {
    width: 100%;
    margin-bottom: 4%;
    padding-left: 5%;
    padding-right: 4%;
}
</style>
