<template>
    <p class="n_side_input"> For example, The number of side is <input type="text" id="nside">
        <el-button size="mini" @click="mouseClick"> Activate </el-button>
    </p>

    <div id="PolygonContainer">
        <canvas id="polygon"></canvas>
        <canvas id="polygonMLP"></canvas>
    </div>

    <p class="img_description"> Figure 3: A single-hidden-layer MLP approximates a circle.</p>
</template>

<script>
import { onMounted } from 'vue'
import { Polygon, DrawPolygon } from '@/engine/polygon.js'

export default {
    name: 'PolygonGraph',
    setup () {
        onMounted(() => {
            function adjustFigure (containerId, canvasIds, ratio) {
                var binarywidth = document.getElementById(containerId).clientWidth
                var binaryheight = binarywidth * ratio
                document.getElementById(containerId).setAttribute('style', 'height:' + binaryheight + 'px')
                for (var i in canvasIds) {
                    document.getElementById(canvasIds[i]).width = binaryheight
                    document.getElementById(canvasIds[i]).height = binaryheight
                    document.getElementById(canvasIds[i]).setAttribute('style', 'width:' + binaryheight + 'px')
                    document.getElementById(canvasIds[i]).setAttribute('style', 'height:' + binaryheight + 'px')
                }
            }
            // Polygon
            adjustFigure('PolygonContainer', ['polygon', 'polygonMLP'], 0.492)
            DrawPolygon()

            window.addEventListener('resize', function () {
                adjustFigure('PolygonContainer', ['polygon', 'polygonMLP'], 0.492)
                DrawPolygon()
            })
        })

        function mouseMove (e) {
        }

        function mouseClick () {
            DrawPolygon()
        }

        return {
            mouseMove,
            mouseClick,
            onMounted
        }
    }
}
</script>

<style scoped>
p.binary_input, p.n_side_input, p.caption {
    text-align: center;
}

.n_side_input .el-button {
    margin-left: 10px;
    margin-bottom: 5px;
}

#nside {
    margin-left: 5px;
    width: 120px;
}

#PolygonContainer{
    display: block;
    width:100%;
}

#polygon{
    display: inline;
    border: 2px solid rgb(190, 187, 187);
}

#polygonMLP {
    display: inline;
    border: 1px solid rgb(190, 187, 187);
    margin-left: 1%;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
