<template>
    <div class="canvas" @mousemove="mouseMove" @click="mouseClick">
         <transition
            enter-active-class='animate__animated animate__fadeIn'
            leave-active-class='animate__animated animate__fadeOut'>
                <canvas v-show="showCanvas"></canvas>
        </transition>
    </div>
</template>

<script>
import { useStore } from 'vuex'
import { onMounted, toRefs, watch } from 'vue'
import { Tree } from '../engine/Tree.js'
// import { Ball } from '../animation/Ball.js'
// import { randomIntFromRange, randomColor } from '../engine/Utility.js'

export default {
    name: 'Canvas',
    setup () {
        console.log('set up - homecanvas')
        const navHeight = 100
        const drawings = {}
        const store = useStore()
        const { showCanvas, pubLec } = toRefs(store.state)
        var mouseX
        var mouseY
        var canvasHandler

        onMounted(() => {
            console.log('onMounted - homecanvas')
            const canvas = document.querySelector('canvas')
            const ctx = canvas.getContext('2d')
            canvas.width = window.screen.width - 10 // 
            canvas.height = window.screen.height - navHeight - 147
            const originX = canvas.width / 2 + 370 // 
            const originY = canvas.height / 2 + 10
            drawings.Tree = new Tree(originX, originY)
            drawings.Tree.init()

            function animate () {
                canvasHandler = requestAnimationFrame(animate)
                ctx.clearRect(0, 0, innerWidth, innerHeight)
                drawings.Tree.update(ctx, mouseX, mouseY)
            }

            watch(showCanvas, (cur, pre) => {
                console.log(cur, pre, 'watch')
                if (pre === false && cur === true) {
                    canvasHandler = requestAnimationFrame(animate)
                } else if (pre === true && cur === false) {
                    cancelAnimationFrame(canvasHandler)
                }
            })
            // animate()
        })

        function mouseMove (e) {
            mouseX = e.x
            mouseY = e.y - navHeight
            const tree = drawings.Tree
            for (const key in tree.nodesDict) {
                const node = tree.nodesDict[key]
                if (pubLec.value.includes(node.id)) {
                    if (mouseX - node.x > -node.curRadius && mouseX - node.x < node.curRadius &&
                        mouseY - node.y > -node.curRadius && mouseY - node.y < node.curRadius) {
                        store.commit('showSummary')
                        store.commit('updateSummaryContent', node.id)
                    }
                }
            }
        }

        function mouseClick () {
            for (const key in drawings.Tree.nodesDict) {
                const node = drawings.Tree.nodesDict[key]
                console.log(node.id)
                if (pubLec.value.includes(node.id)) {
                    if (mouseX - node.x > -node.curRadius && mouseX - node.x < node.curRadius &&
                        mouseY - node.y > -node.curRadius && mouseY - node.y < node.curRadius) {
                        store.commit('showNote')
                        store.commit('updateNoteContent', node.id)
                        break
                    }
                }
            }
        }

        return {
            showCanvas,
            mouseMove,
            mouseClick,
            onMounted
        }
    }
}

// drawings.Balls = reactive([])
