    </p><br>

    <h2 id="lec4-sec-1">Function Optimization <span class="paragraph_author">(Haocheng Han)</span></h2>
    <p>A <b>loss (cost) function</b> quantifies the "goodness" of our model at making predictions for a given set of parameters.
        The loss function has its curve and gradients, and the curve's slope tells us how to update our parameters to make
        the model more accurate. Most Deep Learning tasks' main target is to optimize a loss function (usually finding its
        global minimum) by tuning its parameters.
    </p>
    <img class='image' src="./assets/minimum.png"/>
    <p>To locate such a global minimum,  our first reaction probably is to find a value of x such that the derivative of f(x)
        becomes 0, <b>i.e. f'(x) = 0.</b> However, we need to be careful that the global minimum is <b>NOT</b> the only point with f'(x<span class="sub_text"><sub>min</sub></span>) = 0.
    </p>

    <h3 id="lec4-sec-1-1">Turning Points</h3>
    <p>Except the minimum points, the maximum points also satisfy f'(x<span class="sub_text"><sub>max</sub></span>) = 0 and all of them are called <b>turning points.</b>
       Thus, to judge whether a turning point is maximum or minimum, we also need to check its <b>second derivative f''(x)</b> using the following rules:
    </p>
    <ul>
        <li>f''(x)  0, the turning point is at <b>minima</b></li>
        <li>f''(x)  0, the turning point is at <b>maxima</b></li>
        <li>f''(x) = 0, the turning point is called an <b>inflection point</b></li>
    </ul> <br>
    <img class='image' src="./assets/inflex.png"/>
    <p>To conclude, x is a:</p>
    <ul>
        <li>minimum point if and only if f'(x) = 0 and f''(x)  0</li>
        <li>maximum point if and only if f'(x) = 0 and f''(x)  0</li>
    </ul> <br>

    <h3 id="lec4-sec-1-2">2-Dimension  N-Dimension</h3>
    <p> Now, let's move on to the functions of <b>multiple variables</b>, and the basic idea to find minimum still holds. Suppose a function
        f(x<span class="sub_text"><sub>1</sub></span>,
          x<span class="sub_text"><sub>2</sub></span>,
          x<span class="sub_text"><sub>3</sub></span>, ... ,
          x<span class="sub_text"><sub>n</sub></span>),
        we can then find its minima by the following steps:
    </p>
    <ul>
        <li>(1) Find the <b>turning points z</b> = (
            x<span class="sub_text"><sub>1</sub></span>,
            x<span class="sub_text"><sub>2</sub></span>,
            x<span class="sub_text"><sub>3</sub></span>, ... ,
            x<span class="sub_text"><sub>n</sub></span>) such that f'(z) = 0
        </li>
        <li>(2) To confirm whether z is a minimum or maximum, we need to compute its <b>Hessian Matrix</b>, which is equivalent to find the f''(x)
            in our previous univariable example.
        </li>
        <li>(3) If the Hessian is <b>positive definite</b> (all its eigenvalues are positive), z is a minimum. On the contrary,
            if the Hessian is <b>negative definite</b> (all its eigenvalues are negative), z is a maximum.
        </li>
    </ul> <br>

    <h3 id="lec4-sec-1-3">Closed Solutions NOT Always Available</h3>
    <p> As mentioned previously, we can directly obtain some function's minimum by calculating their first and second derivatives (closed solutions). Nevertheless, some
        complex functions <b>might not have closed solutions</b>. In this case, we need to resort to iterative methods. The following section will
        tell you a story about the most popular iterative method: <b>Gradient Descent.</b>
    </p>

    <!-- Eason -->
    <h2 id="lec4-sec-2">Gradient Descent and Bhiksha <span class="paragraph_author">(Eason)</span></h2>
    <!-- <h3>Initiation Story of Gradient Descent</h3> -->
    <p> Story: In March 1995, Bhiksha took a photo tour on a mountain and had a wonderful day. The scenery on the hill was so beautiful that he accidentally lost track of time.
        Then, the sky gradually darkened, and the temperature began to plummet. Suddenly, the leaves in the forest were rustling in the sudden wind, and he could
        even hear the howling of wolves not far away. Bhiksha became so nervous. He had only one thing in mind: <b>to get down the mountain as fast as he could.</b>
    </p>
    <img class='image' src="./assets/GD_Bhiksha.png"/>
    <p> To descend the mountain in the shortest time, Bhiksha recalls an optimization algorithm, <b>"Gradient Descent,"</b> that he learned from college.
        He pulled out from his backpack a mysterious instrument gifted by his student Eason that could instantly measure a mountain slope's <b>gradient</b>.
        After that, every 100 meters, he would use it to identify the <b>steepest</b> direction in his current position and follow it down the hill. Thanks to god, thirty minutes later,
        Bhiksha made it to the bottom. He is so lucky that he knows Gradient Descent's principles; otherwise, his life would be in danger.
    </p>

    <h3 id="lec4-sec-2-1">The "Life-saving" Algorithm: Gradient Descent (GD)</h3>
    <p>Gradient descent can not only save Bhiksha's life but also can save your life in this course! Within the context of Deep Learning, GD is an optimization algorithm
       used to minimize some functions by <b>iteratively moving in the direction of steepest descent as defined by the negative of the gradient</b>. You will use the GD method
       to update DL model's parameters to find the minimum of the loss function.
    </p>
    <img class='image' src="./assets/GD.png"/>

    <h3 id="lec4-sec-2-2">Learning Rate (LR)</h3>
    <p> An important concept related to GD is learning rate, which controls the step size that you take each time  i.e. "how fast you go down the hill". It's important to note that
        <b>bigger LR is not always better</b>. If LR is too large, we will risk <b>overshooting</b> the minimum point. On the other hand, if LR is too small, we are moving too slow and it will take us a
        very long time to get to the bottom. Thus, LR is a hyper-parameter that needs to carefully tune when you train your model. Take-away: most DL models have no best LR.
        The best LR only comes out of your constant attempts.
    </p>
    <img class='image' src="./assets/LR.png"/>

    <h3 id="lec4-sec-2-3">Math of Gradient Descent</h3>
    <p> Now, we will apply GD to optimize the below cost function f(x) step by step. Again, simply imagine its curve as a 2-D mountain.
        The variable x represents our position along the x-axis, and the variable y represents the mountain height.
    </p>
    <img class='image' src="./assets/slope.png"/>
    <p> Since there are wolves chasing us behind, our target is to get close to the bottom ASAP. No matter where we are on the hill,
        we use our mysterious instrument to calculate the current slope and then make a decision:
    </p>
    <ul>
        <li>(1) If the slope is negative, we go right (increasing x)</li>
        <li>(2) If the slope is positive, we go left (decreasing x)</li>
    </ul> <br>

    <p> We'll make the above decision each step we take. Say the variable <b>k</b> indicates we're taking the k-th step of updating parameter x, and <b>&eta;</b> (learning rate)
        controls our step size. Then, the whole process of iteratively updating our steps to down the hill can be presented by the pseudocode below:
    </p>
    <img class='image' src="./assets/GD_formula.png"/>
    <p>That's it! This is how GD works.</p>

    <h3 id="lec4-sec-2-4">Convergence of Gradient Descent</h3>
    <p>Nevertheless, we should bear in mind that not all mountains in this world look so "friendly". If you are climbing Himalayas of China, its 2-D plot probably looks like this:</p>
    <img class='image' src="./assets/local_minima.png"/>
    <p>In this case, you can hardly reach the bottom with only gradient descent, since you are very easy to be trapped on those <b>local minima</b>.
        The functions with graph like this are called <b>non-convex functions</b>. Some advanced techniques will be introduced in later lectures to overcome this problem.
    </p>

    <!-- Tianhao -->
    <h2 id="lec4-sec-3">Case Study - Animal Image Classifier <span class="paragraph_author">(Tianhao Wang)</span></h2>
    <p>Once youve mastered the concept of gradient descent, you are ready to apply it in real world problems! In the following sections, we'll go through a toy animal image classification
        case study which would show you the DL model training pipeline step by step.
    </p>

    <h3 id="lec4-sec-3-1">Definition of <MathJax :str="'\\( X \\)'" display="inline"/> and <MathJax :str="'\\( Y \\)'" display="inline"/></h3>

    <p> Under supervised learning, datasets are structured as input-output pairs
        <MathJax :str="'\\((X_1, Y_1) (X_N, Y_N) \\)'" display="inline"/>, where <MathJax :str="'\\( X_i \\)'" display="inline"/>
        represents the input features and <MathJax :str="'\\( Y_i \\)'" display="inline"/> represents the output label.
        The meanings of input features and output label vary under different problem settings. For example, in image classification tasks,
        <MathJax :str="'\\( X_i \\)'" display="inline"/> <b>is a vector of image pixel values, and <MathJax :str="'\\( Y_i \\)'" display="inline"/> refers to an image class</b>.
        And in the stock price prediction problems, <MathJax :str="'\\( X_i \\)'" display="inline"/> and <MathJax :str="'\\( Y_i \\)'" display="inline"/> could be trading
        volumes and stock prices, respectively.
    </p>

    <p>In our example, the dataset only has three classes, cat, dog and pig:</p>
    <img class='image' src="./assets/tianhao1.png"/>

    <p>However, we cannot feed images to the neural network directly. Instead, we need to "flatten" the image (a matrix of pixels) into a vector. For the sake of demonstration,
       we assume each picture only consists of 4 pixels, i.e. the input vector has only four dimensions (Note: most images contain far more pixels than 4).
    </p>
    <img class='image' src="./assets/tianhao2.png"/>

    <p>Similarly, we need to convert class labels from strings to
        <button class='hyperlink' v-scroll-to="{ element: '#lec4-sec-4-3', container: '#noteContainer', easing: 'linear' }">one-hot vectors</button>:
    </p>
    <img class='image' src="./assets/tianhao3.png"/>
    <p>To generalize, if the output labels have N categories, they can be represented by N-dimensional one-hot vectors.</p>

    <h3 id="lec4-sec-3-2">Modeling</h3>
    <p>We've already understood the dataset representation, now let's build our first classifier - a simple two-hidden-layers MLP that takes 4-dimensional image vectors as input and output
       3-dimensional vectors as output.
    </p>
    <img class='image' src="./assets/tianhao4.png"/>

    <p>Next, we need to pass the output to a <button class='hyperlink' v-scroll-to="{ element: '#lec4-sec-4-2', container: '#noteContainer', easing: 'linear' }">Softmax</button>
       layer which normalizes the real values to probabilities that sum to 1.
    </p>
    <img class='image' src="./assets/tianhao5.png"/>

    <h3 id="lec4-sec-3-3">Loss and Optimization</h3>
    <p>Then, we select
        <button class='hyperlink' v-scroll-to="{ element: '#lec4-sec-4-4', container: '#noteContainer', easing: 'linear' }">
            <MathJax :str="'\\( KL \\)'" display="inline"/> divergence
        </button>
       (a loss function) to measure the MLP performance:</p>
    <img class='image' src="./assets/tianhao6.png"/>
    <p> Lastly, we'll apply Gradient Descent to iteratively update the model parameters so as to minimize the loss. In the following interactive graph, we will train the network with
        <b> only "Cat"</b> images. You can click "Step" to train the network only one step, or click "Train" to train it 100 steps. The graph will visualize the weight updating process.
    </p>
    <!-- Canvas -->
    <div class="content">
        <CaseStudy />
    </div>
    <p> As you may notice, the more steps we train the network with Gradiant Descent,
        the more likely it would correctly identify the cat images. <b>This means our network is learning!</b>
    </p>

    <!-- Ravi -->
    <h2 id="lec4-sec-4">Other Fundamental Knowledges (Ravi)</h2>
    <h3 id="lec4-sec-4-1">Vector activation</h3>
    <p> Till now, we assumed that every neuron receives an affine combination of input, resulting in a scalar value <MathJax :str="'\\( z \\)'" display="inline"/>,
        and applies an activation function to this scalar value. For a single layer of a neural network we assumed:
    </p>
    <MathJax :str="'\\[ \\displaystyle[y_{1},y_{2},y_{3}.....y_{n}] = [f(z_{1}),f(z_{2}),f(z_{3}).....f(z_{n})] \\]'" display="block"/>

    <p>This assumption means that changes in values of <MathJax :str="'\\( z_{i} \\)'" display="inline"/>
       affects the value of <MathJax :str="'\\( y_{j} \\)'" display="inline"/> <b>only when</b>
       <MathJax :str="'\\( i = j \\)'" display="inline"/>.
       For example, changes in values of <MathJax :str="'\\( z_{2},z_{3}.....z_{n} \\)'" display="inline"/>
        do not change the value of <MathJax :str="'\\( y_{1} \\)'" display="inline"/>.
    </p>

    <p>However, in the case of vector activation, for a single layer of a neural network the output is calculated by applying the activation functions on <b>a set of inputs:</b></p>
    <MathJax :str="'\\[ \\displaystyle[y_{1},y_{2},y_{3}.....y_{n}] = f([z_{1}, z_{2},z_{3}.....z_{n}]) \\]'" display="block"/>

    <p> This would mean that a change in <b>any</b> one of the values of <MathJax :str="'\\( z_{1},z_{2}......z_{n} \\)'" display="inline"/>
        can change the value of output vector.
        The same is illustrated below through a layer containing four neurons:
    </p>
    <img class='image' src="./assets/activation.png"/>

    <h3 id="lec4-sec-4-2">Softmax</h3>
    <p> Softmax is a vector activation function which takes a real-valued vector as input and returns a vector containing values that sum to 1.
        The elements of the input vector can be any real value, the softmax function appropriately scales them between 0 and 1.
        The output of the softmax function can be interpreted as probabilities. The larger the value of the input, the higher is the probability and vice versa.
    </p>
    <p> The softmax activation function is mainly used in neural network models for multi-class classification problems. In such models,
        the real valued scores are normalized by the softmax function, converting them into probabilities that sum to one. Each value in
        the output of the softmax function is interpreted as the probability of membership for each class. The formula for softmax is as follows:
    </p>
    <MathJax :str="'\\[ \\displaystyle y_{i} = \\frac{e^{z_{i}}}{\\sum_{j}e^{z_{j}}} \\]'" display="block"/>

    <p> For example, if the input is <MathJax :str="'\\( [1, 5, 4]^{T} \\)'" display="inline"/>, then the output vector can be calculated as:</p>
    <ul>
        <li> <MathJax :str="'\\( y_{1} = e^{1}/(e^{1}+e^{5}+e^{4})=0.0130 \\)'" display="inline"/> </li>
        <li> <MathJax :str="'\\( y_{2} = e^{5}/(e^{1}+e^{5}+e^{4})=0.7213 \\)'" display="inline"/> </li>
        <li> <MathJax :str="'\\( y_{3} = e^{4}/(e^{1}+e^{5}+e^{4})=0.2653 \\)'" display="inline"/> </li>
    </ul> <br>

    <h3 id="lec4-sec-4-3">One-Hot Encoding</h3>
    <p>Training a neural network involves minimizing loss which in turn involves computing the divergence. Divergence can be computed from desired output d and actual output
        <MathJax :str="'\\( y \\)'" display="inline"/>. For multi-class classifications, the actual output is generally computed by the softmax activation present in the final
        layer. The desired output is generally represented as a one-hot vector. For example, consider a neural network that classifies a cat, a dog, and a pig.
        They can be represented as a vector as follows:
    </p>
    <p style="text-align:center;">
        <MathJax :str="'\\( [cat, dog, pig]^{T} \\)'" display="inline"/>
    </p>
    <p>The desired output for the above classes can be encoded as:</p>
    <ul>
        <li>&nbsp;&nbsp; <MathJax :str="'\\( cat\: [1, 0, 0]^{T} \\)'" display="inline"/></li>
        <li>&nbsp;       <MathJax :str="'\\( dog\: [0, 1, 0]^{T} \\)'" display="inline"/></li>
        <li>&nbsp;&nbsp; <MathJax :str="'\\( pig\: [0, 0, 1]^{T} \\)'" display="inline"/></li>
    </ul> <br>
    <p> <b>This sparse representation is called one-hot encoding</b>. In general, if there are <MathJax :str="'\\( n \\)'" display="inline"/> classes,
        the one-hot vector will contain <MathJax :str="'\\( n \\)'" display="inline"/> - 1 zeros and single 1 at the position of the class.</p>

    <h3 id="lec4-sec-4-4">Divergence Function</h3>
    <p> Now that we have our desired output as a one-hot vector and our expected output from the model, we can calculate divergence.
        One simple way of doing it is by using the <MathJax :str="'\\( L_{1} \\)'" display="inline"/> divergence:
    </p>
    <MathJax :str="'\\[ \\displaystyle Div(Y, d) = \\sum_{i} |y_{i} - d_{i}| \\]'" display="block"/>

    <p> The above divergence is not differentiable at <MathJax :str="'\\( y_{i} = y_{d} \\)'" display="inline"/>
        (at all other values of <MathJax :str="'\\( y_{i} \\)'" display="inline"/>, derivative = -1 or +1). As you will see later, differentiability is an
        important property in training our neural network. So for real-valued output vectors, we prefer <MathJax :str="'\\( L_{2} \\)'" display="inline"/> divergence:
    </p>
    <MathJax :str="'\\[ \\displaystyle Div(Y,d) = \\frac{1}{2}\\sum_{i}(y_{i} - d_{i})^2 \\]'" display="block"/>
    <MathJax :str="'\\[ \\displaystyle\\frac{dDiv(Y,d)}{dy_{i}} = (y_{i} - d_{i}) \\]'" display="block"/>

    <p> Note that the derivative becomes 0, when <MathJax :str="'\\( y_{i} = d_{i} \\)'" display="inline"/>. For classification tasks, we use the
       <b>Kullback Leibler (<MathJax :str="'\\( KL \\)'" display="inline"/>) divergence.</b></p>
    <p>Binary tasks:</p>
    <MathJax :str="'\\[ Div(Y, d) = -d \\log(Y) - (1-d)\\log(1-Y) \\]'" display="block"/>

    <p>Multi-class classification:</p>
    <MathJax :str="'\\[ Div(Y,d)=\\sum_{i}d_{i}\\log d_{i}-\\sum_{i}d_{i}\\log y_{i}=-\\log y_{c} \\]'" display="block"/>

    <p>
        If we increase <MathJax :str="'\\( y_{c} \\)'" display="inline"/> , <MathJax :str="'\\( -\log y_{c} \\)'" display="inline"/> decreases.
        Hence the divergence decreases. We can also observe that the derivative of
        divergence is not 0 (<MathJax :str="'\\( \\frac{dDiv(Y,d)}{dy_{c}}=-\\frac{1}{y_{c}} \\)'" display="inline"/>) even when <MathJax :str="'\\( y=d \\)'" display="inline"/>.
    </p>
    <p> As you can observe in the graph below, the slope of <MathJax :str="'\\( KL \\)'" display="inline"/> divergence is much steeper
        than that of <MathJax :str="'\\( L_{2} \\)'" display="inline"/>  divergence. While training a neural network,
        this property facilitates quicker convergence of gradient descent.
    </p>
    <img class='image' src="./assets/ravi_picture.png"/>
    <p> Please note that <MathJax :str="'\\( KL \\)'" display="inline"/> divergence is explained here in the context of training neural networks.
        In general, <MathJax :str="'\\( KL \\)'" display="inline"/> divergence is used to measure how one probability distribution differs from a second,
        reference probability distribution. Learn more about <MathJax :str="'\\( KL \\)'" display="inline"/> divergence here:
        <a class='hyperlink' href="https://en.wikipedia.org/wiki/KullbackLeibler_divergence" target="_blank">https://en.wikipedia.org/wiki/KullbackLeibler_divergence</a>
    </p>
    <br><br><br><br><br><br><br><br>
</template>

