let isMathjaxConfig = false
const initMathjaxConfig = () => {
    if (!window.MathJax) {
        return
    }
    window.MathJax.Hub.Config({
        showProcessingMessages: false,
        messageStyle: 'none',
        jax: ['input/TeX', 'output/HTML-CSS'],
        tex2jax: {
            inlineMath: [['$', '$'], ['\\(', '\\)']],
            displayMath: [['$$', '$$'], ['\\[', '\\]']],
            skipTags: ['script', 'noscript', 'style', 'textarea', 'pre', 'code', 'a'],
            processEscapes: true,
            ignoreClass: 'tex2jax_ignore|dno'
        },
        'HTML-CSS': {
            availableFonts: ['STIX', 'TeX'],
            showMathMenu: false
        }
    })
    isMathjaxConfig = true
}

const MathQueue = function (elementId) {
    if (!window.MathJax) {
        return
    }
    window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, document.getElementsByClassName(elementId)]) // class
//  window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, document.getElementById(elementId)]) // id
}

export default {
    isMathjaxConfig,
    initMathjaxConfig,
    MathQueue
}
