<template>
  <div class="latex" v-html="str" :style="{ display }"/>
</template>

<script>
import MathJax from './math-jax'
export default {
    name: 'TheLatex2Math',
    props: {
        str: {
            type: String,
            default: ''
        },
        display: {
            type: String,
            default: 'block'
        }
    },
    mounted () {
        this.updateDom()
    },
    watch: {
        latex () {
            this.updateDom()
        }
    },
    methods: {
        updateDom () {
            this.$nextTick(() => {
                MathJax.isMathjaxConfig && MathJax.initMathjaxConfig()
                MathJax.MathQueue('latex')
                this.$nextTick(() => {
                    console.log(this.$el)
                })
                // .style.display = 'inline'
            })
        }
    }
}
</script>

<style>
.mjx-chtml {
  display: inherit !important;
}
</style>
