import { createStore } from 'vuex'

export default createStore({
    state: {
        // Published lectures
        pubLec: ['Lec 2', 'Lec 4', 'Lec 6', 'Lec 7'],

        showNote: false,
        showIntro: true,
        showCanvas: false,
        showSummary: false,
        lecName: '',
        name: 'dell',
        summaryContent: 'Lec 4'
    },
    mutations: {
        showNote () {
            this.state.showNote = true // this.state.showNote! not this.showNote
            this.state.showCanvas = false
            this.state.showSummary = false
        },
        hideNote () {
            this.state.showNote = false
            this.state.showCanvas = true
            this.state.showSummary = true
        },
        hideIntroShowCanvas () {
            this.state.showIntro = false
            this.state.showCanvas = true
            this.state.showSummary = true
        },
        showCanvas () {
            this.state.showCanvas = true
        },
        showSummary () {
            this.state.showSummary = true
        },
        updateSummaryContent (state, str) {
            this.state.summaryContent = str
        },
        updateNoteContent (state, lecName) {
            this.state.lecName = lecName
        }
    },
    actions: {
    },
    modules: {
    }
})
