<template>
    <div class="neuronGrid">
        <div class='neuronParam'>N<sub>1</sub></div>
        <div class='neuronParam'>N<sub>2</sub></div>
        <div class='neuronParam'>N<sub>3</sub></div>
        <div class='neuronParam'>N<sub>4</sub></div>
        <div class="layerInputs">
            <el-input type="number" v-model="num1" min="0" max="10" placeholder="Enter N1"></el-input>
        </div>
        <div class="layerInputs">
            <el-input type="number" v-model="num2" min="0" max="10" placeholder="Enter N2"></el-input>
        </div>
        <div class="layerInputs">
            <el-input type="number" v-model="num3" min="0" max="10" placeholder="Enter N3"></el-input>
        </div>
        <div class="layerInputs">
            <el-input type="number" v-model="num4" min="0" max="10" placeholder="Enter N4"></el-input>
        </div>
    </div>

    <div
        id="mlpCanvasContainter"
        @mousemove="mouseMove"
        @click="mouseClick"
    >
        <canvas id="mlpCanvas"> </canvas>
    </div>
    <p class="img_description"> Figure 2 </p>
</template>

<script>
import { ref, onMounted, reactive, watch } from 'vue'
import { MLP } from '@/engine/MLP.js'
import { Layer } from '@/engine/Layer.js'
import * as util from '@/utils/util.js'

export default {
    name: 'MLP',
    setup () {
        let mouseX
        let mouseY
        var nodesDict = {}
        var linesArray = []
        const num1 = ref(6)
        const num2 = ref(10)
        const num3 = ref(4)
        const num4 = ref(3)
        const numMin = 0
        const numMax = 10

        onMounted(() => {
            const canvasContainer = document.getElementById('mlpCanvasContainter')
            const canvas = document.getElementById('mlpCanvas')
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth
            canvas.height = canvasContainer.offsetHeight
            var layersInfo = [
                {
                    numNodes: num1.value,
                    radius: undefined,
                    color: util.wineRed
                },
                {
                    numNodes: num2.value,
                    radius: undefined,
                    color: util.wineRed
                },
                {
                    numNodes: num3.value,
                    radius: undefined,
                    color: util.wineRed
                },
                {
                    numNodes: num4.value,
                    radius: undefined,
                    color: util.wineRed
                }
            ]

            function drawMLP (width, height) {
                const numLayers = layersInfo.length
                const originX = document.getElementById('mlpCanvasContainter').offsetWidth * 0.29
                const originY = document.getElementById('mlpCanvasContainter').offsetHeight * 0.5
                const radius = 10 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                const xInterval = 100 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                const yIntervalArray = []
                for (var i = 0; i < numLayers; i++) {
                    yIntervalArray.push(40 * document.getElementById('mlpCanvasContainter').offsetWidth / 680)
                }

                // Update radius
                for (const key in layersInfo) { layersInfo[key].radius = radius }

                var mlp = new MLP(originX, originY, layersInfo, xInterval, yIntervalArray)
                mlp.addToEngine(nodesDict)
                mlp.connectAll(linesArray)

                watch(num1, (cur, pre) => {
                    if (cur > numMax) {
                        num1.value = numMax
                    } else if (cur <= numMin) {
                        num1.value = numMin
                        num2.value = 0
                        num3.value = 0
                        num4.value = 0
                    }
                    if (num1.value[0] === '0' && num1.value.length > 1) { num1.value = num1.value.slice(1) }
                    if (cur > numMax) { num1.value = numMax } else if (cur < numMin) { num1.value = numMin }
                    layersInfo[0].numNodes = cur
                    const numLayers = layersInfo.length
                    const originX = document.getElementById('mlpCanvasContainter').offsetWidth * 0.29
                    const originY = document.getElementById('mlpCanvasContainter').offsetHeight * 0.5
                    const radius = 10 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const xInterval = 100 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const yIntervalArray = []
                    for (var i = 0; i < numLayers; i++) {
                        yIntervalArray.push(40 * document.getElementById('mlpCanvasContainter').offsetWidth / 680)
                    }
                    mlp = new MLP(originX, originY, layersInfo, xInterval, yIntervalArray)
                    nodesDict = {}
                    linesArray = []
                    mlp.addToEngine(nodesDict)
                    mlp.connectAll(linesArray)
                })

                watch(num2, (cur, pre) => {
                    if (cur > numMax) {
                        num2.value = numMax
                    } else if (cur <= numMin) {
                        num2.value = numMin
                        num3.value = 0
                        num4.value = 0
                    }
                    if (num2.value[0] === '0' && num2.value.length > 1) { num2.value = num2.value.slice(1) }
                    if (cur > numMax) { num2.value = numMax } else if (cur < numMin) { num2.value = numMin }
                    layersInfo[1].numNodes = cur
                    const numLayers = layersInfo.length
                    const originX = document.getElementById('mlpCanvasContainter').offsetWidth * 0.29
                    const originY = document.getElementById('mlpCanvasContainter').offsetHeight * 0.5
                    const radius = 10 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const xInterval = 100 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const yIntervalArray = []
                    for (var i = 0; i < numLayers; i++) {
                        yIntervalArray.push(40 * document.getElementById('mlpCanvasContainter').offsetWidth / 680)
                    }
                    mlp = new MLP(originX, originY, layersInfo, xInterval, yIntervalArray)
                    nodesDict = {}
                    linesArray = []
                    mlp.addToEngine(nodesDict)
                    mlp.connectAll(linesArray)
                })

                watch(num3, (cur, pre) => {
                    if (cur > numMax) {
                        num3.value = numMax
                    } else if (cur <= numMin) {
                        num3.value = numMin
                        num4.value = 0
                    }
                    if (num3.value[0] === '0' && num3.value.length > 1) { num3.value = num3.value.slice(1) }
                    if (cur > numMax) { num3.value = numMax } else if (cur < numMin) { num3.value = numMin }
                    layersInfo[2].numNodes = cur
                    const numLayers = layersInfo.length
                    const originX = document.getElementById('mlpCanvasContainter').offsetWidth * 0.29
                    const originY = document.getElementById('mlpCanvasContainter').offsetHeight * 0.5
                    const radius = 10 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const xInterval = 100 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const yIntervalArray = []
                    for (var i = 0; i < numLayers; i++) {
                        yIntervalArray.push(40 * document.getElementById('mlpCanvasContainter').offsetWidth / 680)
                    }
                    mlp = new MLP(originX, originY, layersInfo, xInterval, yIntervalArray)
                    nodesDict = {}
                    linesArray = []
                    mlp.addToEngine(nodesDict)
                    mlp.connectAll(linesArray)
                })

                watch(num4, (cur, pre) => {
                    if (cur > numMax) {
                        num4.value = numMax
                    } else if (cur <= numMin) {
                        num4.value = numMin
                    }
                    if (num4.value[0] === '0' && num4.value.length > 1) { num4.value = num4.value.slice(1) }
                    if (cur > numMax) { num4.value = numMax } else if (cur < numMin) { num4.value = numMin }
                    layersInfo[3].numNodes = cur
                    const numLayers = layersInfo.length
                    const originX = document.getElementById('mlpCanvasContainter').offsetWidth * 0.29
                    const originY = document.getElementById('mlpCanvasContainter').offsetHeight * 0.5
                    const radius = 10 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const xInterval = 100 * document.getElementById('mlpCanvasContainter').offsetWidth / 680
                    const yIntervalArray = []
                    for (var i = 0; i < numLayers; i++) {
                        yIntervalArray.push(40 * document.getElementById('mlpCanvasContainter').offsetWidth / 680)
                    }
                    mlp = new MLP(originX, originY, layersInfo, xInterval, yIntervalArray)
                    nodesDict = {}
                    linesArray = []
                    mlp.addToEngine(nodesDict)
                    mlp.connectAll(linesArray)
                })
            }

            drawMLP(canvas.width, canvas.height)

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                nodesDict = {}
                linesArray = []
                drawMLP(canvas.width, canvas.height)
            })

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, innerWidth, innerHeight)

                for (var i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }

                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX, mouseY)
                }
            }
            animate()
        })

        function mouseMove (e) {
            const canvas = document.getElementById('mlpCanvas')
            var bounds = canvas.getBoundingClientRect()
            mouseX = e.pageX - bounds.left - scrollX
            mouseY = e.pageY - bounds.top - scrollY
            mouseX /= bounds.width
            mouseY /= bounds.height
            mouseX *= canvas.width
            mouseY *= canvas.height
        }

        return {
            mouseMove,
            onMounted,
            num1,
            num2,
            num3,
            num4
        }
    }
}
</script>

<style scoped>
#mlpCanvasContainter {
    display: block;
    width: 100%;
    height: 450px;
    border: 1px solid rgb(190, 187, 187);
}

#mlpCanvas {
    border: 0px solid red;
}

input {
    width: 80%;
    font-family: "Open Sans", sans-serif;
    font-size: 15px;
    text-align: center;
    padding: 3px;
    margin: 0% 10%;
}

.neuronGrid {
    display: grid;
    grid-template-rows: repeat(2, 50%);
    grid-template-columns: 1fr 1fr 1fr 1fr;
    border: 0px solid black;
    padding-top: 12px;
    padding-bottom: 12px;
}

.neuronParam {
    text-align: center;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
}

.layerInputs .el-input {
    margin-left: 12%;
    width: 75%;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
