<template>
    <transition
        enter-active-class='animate__animated animate__fadeIn'
        leave-active-class='animate__animated animate__fadeOut'>
        <div class="summary" v-if="showSummary">
            <h2 class="summaryContent">{{ summaryContent }}</h2>
            <Lec2Intro class='content' v-if="summaryContent === 'Lec 2'"/>
            <Lec4Intro class='content' v-if="summaryContent === 'Lec 4'"/>
        </div>
    </transition>
</template>

<script>
import { useStore } from 'vuex'
import { toRefs } from 'vue'
import Lec2Intro from '@/notes/Lec2/components/Lec2Intro.vue'
import Lec4Intro from '@/notes/Lec4/components/Lec4Intro.vue'

export default {
    name: 'Summary',
    components: {
        Lec2Intro,
        Lec4Intro
    },
    setup () {
        const store = useStore()
        const { showSummary, summaryContent } = toRefs(store.state)
        return {
            showSummary,
            summaryContent,
            Lec2Intro,
            Lec4Intro
        }
    }
}
</script>

<style scoped>
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css');
