@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap');

.summary {
    position: absolute;
    width: 530px;
    height: 530px;
    left: 5%;
    top: 25%;
    background-color: rgba(236, 236, 236, 0.5);
    border: 0px solid black;

    /* font-family: "Montserrat", sans-serif; */
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: black;
    text-decoration: none;

    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr;
}

/* .summary p {
    padding-left: 100px;
} */

h2 {
    justify-self: start;
    grid-column: 0/1;
    padding-left: 10px;
    padding-top: 10px;
    font-size: 18px;
    border: 0px solid black;
}

/* .summaryContent div {
    position: absolute;
    justify-self: start;
    grid-column: 1/2;
    margin-top: 50px;
    border: 1px solid black;
} */
.content {
    border: 0px solid black;
