<template>
    <div id="canvasContainter" @mousemove="mouseMove" @click="mouseClick">
        <canvas id="canvas"> </canvas>
    </div>
    <p class="img_description"> Figure 1 </p>
    <div class="perceptron_grid">
        <div class="perceptron_para">  x<sub>1</sub> </div>
        <div class="perceptron_para">  x<sub>2</sub> </div>
        <div class="perceptron_para">  w<sub>1</sub> </div>
        <div class="perceptron_para">  w<sub>2</sub> </div>
        <div class="perceptron_para"> b </div>
        <div><input type="number" v-model='x1' /></div>
        <div><input type="number" v-model='x2' /></div>
        <div><input type="number" v-model='w1' /></div>
        <div><input type="number" v-model='w2' /></div>
        <div><input type="number" v-model='b' /></div>
    </div>
</template>

<script>
import { ref, onMounted, watch } from 'vue'
import { Node, createNodes } from '@/engine/Node.js'
import { Text } from '@/engine/Text.js'
import * as util from '@/utils/util.js'

export default {
    name: 'Perceptron',
    setup () {
        var mouseX
        var mouseY
        var nodesDict = {}
        var linesArray = []
        const x1 = ref(1)
        watch(x1, (cur, pre) => {
            if (cur < -2000) { x1.value = -2000 } else if (cur > 2000) { x1.value = 2000 }
            nodesDict['x1'].text.text = 'x1' + ' = ' + x1.value
            calculation()
        })
        const x2 = ref(1)
        watch(x2, (cur, pre) => {
            if (cur < -2000) { x2.value = -2000 } else if (cur > 2000) { x2.value = 2000 }
            nodesDict['x2'].text.text = 'x2' + ' = ' + x2.value
            calculation()
        })
        const w1 = ref(1)
        watch(w1, (cur, pre) => {
            if (cur < -2000) { w1.value = -2000 } else if (cur > 2000) { w1.value = 2000 }
            linesArray[0].text.text = 'w1' + ' = ' + w1.value
            calculation()
        })
        const w2 = ref(1)
        watch(w2, (cur, pre) => {
            if (cur < -2000) { w2.value = -2000 } else if (cur > 2000) { w2.value = 2000 }
            linesArray[1].text.text = 'w2' + ' = ' + w2.value
            calculation()
        })
        const b = ref(-100)
        watch(b, (cur, pre) => {
            if (cur < -2000) { b.value = -2000 } else if (cur > 2000) { b.value = 2000 }
            nodesDict['b'].text.text = 'b' + ' = ' + b.value
            calculation()
        })
        const z = ref(0)

        function updateZ () {
            // console.log(x1.value * w1.value + x2.value * w2.value + b.value)
            z.value = (x1.value * w1.value + x2.value * w2.value + parseInt(b.value)).toFixed(3)
        }

        function calculation () {
            updateZ()
            nodesDict['z'].text.text = 'z = ' + z.value
            if (z.value > 0) {
                nodesDict['y'].color = util.wineRed
                nodesDict['y'].text.text = 'y (FIRED)'
            } else {
                nodesDict['y'].color = util.grey
                nodesDict['y'].text.text = 'y'
            }
        }

        onMounted(() => {
            const canvasContainer = document.getElementById('canvasContainter')
            const canvas = document.getElementById('canvas')
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth
            canvas.height = canvasContainer.offsetHeight
            const inputRadius = 12

            updateZ()
            function drawPerceptron (width, height) {
                const originX = width * 0.42
                const originY = height * 0.60
                const inputRadius = width / 56
                const fontSize = 16 * width / 680 + 'px'

                const rootInfo = {
                    id: 'root',
                    x: originX,
                    y: originY,
                    // initRadius: 30,
                    initRadius: width / 23,
                    color: util.purple
                }
                var root = new Node(rootInfo)
                nodesDict['root'] = root

                const nodesInfo = [
                    {
                        id: 'x1',
                        radian: 10 / 12 * Math.PI,
                        initRadius: inputRadius,
                        dist: width / 3.7,
                        color: util.orange,
                        textInfo: {
                            text: 'x1 = ' + x1.value,
                            color: util.black,
                            size: fontSize,
                            dx: 0,
                            dy: -25 * width / 680
                        },
                        lineRadian: [Math.PI, undefined],
                        lineTextInfo: {
                            lineText: 'w1 = ' + w1.value,
                            dx: 2 * width / 680,
                            dy: -17 * width / 680,
                            lineTextSize: fontSize
                        }
                    },
                    {
                        id: 'x2',
                        radian: -10 / 12 * Math.PI,
                        initRadius: inputRadius,
                        dist: width / 3.7,
                        color: util.orange,
                        textInfo: {
                            text: 'x2 = ' + x2.value,
                            color: util.black,
                            size: fontSize,
                            dx: 0,
                            dy: 25 * width / 680
                        },
                        lineRadian: [Math.PI, undefined],
                        lineTextInfo: {
                            lineText: 'w2 = ' + w2.value,
                            dx: 2 * width / 680,
                            dy: 17 * width / 680,
                            lineTextSize: fontSize
                        }
                    },
                    {
                        id: 'b',
                        radian: -1 / 2 * Math.PI,
                        initRadius: inputRadius,
                        dist: width / 5.6,
                        color: util.yellow,
                        textInfo: {
                            text: 'b = ' + b.value,
                            color: util.black,
                            size: fontSize,
                            dx: 0,
                            dy: 22 * width / 680
                        }
                    },
                    {
                        id: 'z',
                        radian: 0,
                        initRadius: inputRadius,
                        dist: width / 5.6,
                        color: util.blue,
                        textInfo: {
                            text: 'z = ' + z.value,
                            color: util.black,
                            size: fontSize,
                            dx: 0,
                            dy: -25 * width / 680
                        }
                    }
                ]
                createNodes(root, nodesInfo, nodesDict, linesArray)

                // Darw output node
                const y = [{
                    id: 'y',
                    radian: 0,
                    initRadius: width / 45,
                    dist: width / 4.8,
                    color: util.grey,
                    textInfo: {
                        text: 'y',
                        color: util.black,
                        size: fontSize,
                        dx: 0,
                        dy: -29 * width / 680
                    }
                }]
                createNodes(nodesDict['z'], y, nodesDict, linesArray)

                // Draw description texts on the top
                const yIntervel = 27 * width / 680
                const textRadius = width / 85
                const textX = width / 22

                var bodyDescrip = new Node({ id: 'bodyDescrip', x: textX, y: 25, initRadius: textRadius, color: util.purple })
                bodyDescrip.setText(new Text({ text: 'Main Body : receives several inputs outside', size: fontSize }), 180 * width / 680, 2)
                nodesDict['bodyDescrip'] = bodyDescrip

                var inputDescrip = new Node({ id: 'inputDescrip', x: textX, y: 25 + yIntervel, initRadius: textRadius, color: util.orange })
                inputDescrip.setText(new Text({ text: 'Inputs : the data fed into the main body', size: fontSize }), 170 * width / 680, 2)
                nodesDict['inputDescrip'] = inputDescrip

                var bDescrip = new Node({ id: 'bDescrip', x: textX, y: 25 + yIntervel * 2, initRadius: textRadius, color: util.yellow })
                bDescrip.setText(new Text({ text: 'Bias : controls how hard the perceptron will fire', size: fontSize }), 200 * width / 680, 2)
                nodesDict['bDescrip'] = bDescrip

                var zDescrip = new Node({ id: 'zDescrip', x: textX, y: 25 + yIntervel * 3, initRadius: textRadius, color: util.blue })
                zDescrip.setText(new Text({ text: 'z : the intermidiate output from the main body', size: fontSize }), 195 * width / 680, 2)
                nodesDict['zDescrip'] = zDescrip

                // var actiDescrip = new Node({ id: 'actiDescrip', x: textX, y: 25 + yIntervel * 4, initRadius: textRadius, color: util.purple })
                // actiDescrip.setText(new Text({ text: ': the activation that compresses the intermediate value', size: fontSize }), 229 * width / 680, 2)
                // nodesDict['actiDescrip'] = actiDescrip

                var yDescrip = new Node({ id: 'actiDescrip', x: textX, y: 25 + yIntervel * 4, initRadius: textRadius, color: util.grey })
                yDescrip.setText(new Text({ text: 'y: the final value after compression, either 0 or 1', size: fontSize }), 205 * width / 680, 2)
                nodesDict['yDescrip'] = yDescrip
            }

            drawPerceptron(canvas.width, canvas.height)

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                nodesDict = {}
                linesArray = []
                drawPerceptron(canvas.width, canvas.height)
            })

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, innerWidth, innerHeight)

                for (var i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }

                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX, mouseY)
                }
            }
            animate()
        })

        function mouseMove (e) {
            const canvas = document.getElementById('canvas')
            var bounds = canvas.getBoundingClientRect()
            mouseX = e.pageX - bounds.left - scrollX
            mouseY = e.pageY - bounds.top - scrollY
            mouseX /= bounds.width
            mouseY /= bounds.height
            mouseX *= canvas.width
            mouseY *= canvas.height
        }

        return {
            // eslint-disable-next-line object-property-newline
            x1, x2, w1, w2, b, z,
            mouseMove,
            onMounted
        }
    }
}
</script>

<style scoped>
#canvasContainter {
    display: block;
    width: 100%;
    height: 550px;
    border: 1px solid rgb(190, 187, 187);
}

#canvas {
    border: 0px solid red;
}

input {
    width: 80%;
    font-family: "Open Sans", sans-serif;
    font-size: 15px;
    text-align: center;
    padding: 3px;
    margin: 0% 10%;
}

.perceptron_grid {
    display: grid;
    grid-template-rows: repeat(2, 50%);
    grid-template-columns: repeat(5, 20%);
    border: 0px solid black;
    padding-top: 12px;
    padding-bottom: 12px;
}
.perceptron_para {
    text-align: center;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
}

.img_description {
    padding: 10px;
    text-align: center;
    font-size: 17px;
    color: rgb(37, 37, 37);
}
</style>
