<template>
    <img v-show='fase' id="cat" src="@/notes/Lec4/assets/cat.jpg">
    <img v-show='fase' id="dog" src="@/notes/Lec4/assets/dog.png">
    <img v-show='fase' id="pig" src="@/notes/Lec4/assets/pig.png">
    <div class="btnGroup">
        <el-button id="btnStep" size="small" @click="oneStep"> Step </el-button>
        <el-button id="btnTrain" size="small" @click="train"> Train </el-button>
        <el-button id="btnReset" size="small" @click="resetStep"> Reset </el-button>
        <span class="stepValue"> Step: {{step + 1}}</span>
    </div>
    <!-- <button class='btn_step' type="button" @click="oneStep"> Step </button>
    <button class='btn_train' type="button" @click="train"> Train </button>
    <button class='btn_reset' type="button" @click="resetStep"> Reset </button> -->
    <div
        id="csCanvasContainter"
        @mousemove="mouseMove"
        >
            <canvas @mousemove="mouseMove" id='csCanvas' class="case-study"></canvas>
        </div>
</template>

<script>
import { ref, onMounted, watch } from 'vue'
import { MLP } from '@/engine/MLP.js'
import { getNodePosition, getWeights, getPreds } from '../assets/utils.js'
import * as util from '@/utils/util.js'
import { Text } from '@/engine/Text.js'
// import { useStore } from 'vuex'
// import { toRefs } from 'vue'

export default {
    name: 'CaseStudy',
    setup () {
        const mouseX = ref()
        const mouseY = ref()
        var mlp
        var nodesDict = {}
        var linesArray = []
        const step = ref(0)

        function oneStep () {
            if (step.value < 99) {
                step.value += 1
            }
        }

        function train () {
            var train = window.setInterval(function () {
                if (step.value === 99) {
                    clearInterval(train)
                } else {
                    step.value += 1
                }
            }, 50)
        }

        function resetStep () {
            step.value = 0
        }

        function mouseMove (e) {
            const canvas = document.getElementById('csCanvas')
            var bounds = canvas.getBoundingClientRect()
            mouseX.value = e.pageX - bounds.left - scrollX
            mouseY.value = e.pageY - bounds.top - scrollY
            mouseX.value /= bounds.width
            mouseY.value /= bounds.height
            mouseX.value *= canvas.width
            mouseY.value *= canvas.height
        }
        onMounted(() => {
            const canvasContainer = document.getElementById('csCanvasContainter')
            const canvas = document.getElementById('csCanvas')
            const ctx = canvas.getContext('2d')
            canvas.width = canvasContainer.offsetWidth
            canvas.height = canvasContainer.offsetHeight

            const defaultWidth = 493
            const defaultHeight = 300

            // Load jsons
            const weights = getWeights()
            const preds = getPreds()

            function init (width, height) {
                nodesDict = {}
                linesArray = []
                const layersInfo = [
                    {
                        numNodes: 4,
                        radius: 11 / defaultWidth * width,
                        color: '#db1c4c'
                    },
                    {
                        numNodes: 5,
                        radius: 11 / defaultWidth * width,
                        color: '#BBBBBB'
                    },
                    {
                        numNodes: 5,
                        radius: 11 / defaultWidth * width,
                        color: '#BBBBBB'
                    },
                    {
                        numNodes: 3,
                        radius: 11 / defaultWidth * width,
                        color: '#db1c4c'
                    }
                ]
                const yIntervalArray = []
                for (var i = 0; i < layersInfo.length; i++) {
                    yIntervalArray.push(0.177 * height)
                }
                mlp = new MLP(0.175 * width, 0.5 * height, layersInfo, 0.20 * width, yIntervalArray)
                mlp.connectAll(linesArray)
                mlp.addToEngine(nodesDict)

                nodesDict['x41'].setText(new Text({ text: preds[(1).toString()][0], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
                nodesDict['x42'].setText(new Text({ text: preds[(1).toString()][1], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
                nodesDict['x43'].setText(new Text({ text: preds[(1).toString()][2], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
            }

            window.addEventListener('resize', function () {
                canvas.width = canvasContainer.offsetWidth
                canvas.height = canvasContainer.offsetHeight
                init(canvas.width, canvas.height)
            })

            watch(step, (cur, pre) => {
                nodesDict['x41'].setText(new Text({ text: preds[(cur + 1).toString()][0], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
                nodesDict['x42'].setText(new Text({ text: preds[(cur + 1).toString()][1], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
                nodesDict['x43'].setText(new Text({ text: preds[(cur + 1).toString()][2], size: 11 / defaultWidth * canvas.width + 'px' }), 65 / 13 * 13 / defaultWidth * canvas.width, 0)
            })

            var selectedNodeLineChildren

            function animate () {
                requestAnimationFrame(animate)
                ctx.clearRect(0, 0, canvas.width, canvas.height)

                // Update images
                ctx.drawImage(document.getElementById('cat'), nodesDict['x41'].x + nodesDict['x41'].curRadius + 5,  nodesDict['x41'].y - nodesDict['x41'].initRadius - 4)
                ctx.drawImage(document.getElementById('dog'), nodesDict['x42'].x + nodesDict['x42'].curRadius + 5,  nodesDict['x42'].y - nodesDict['x42'].initRadius - 4)
                ctx.drawImage(document.getElementById('pig'), nodesDict['x43'].x + nodesDict['x43'].curRadius + 5,  nodesDict['x43'].y - nodesDict['x43'].initRadius - 4)

                // Update lines
                for (let i = 0; i < linesArray.length; i++) {
                    linesArray[i].update(ctx)
                }

                // Update nodes
                for (const key in nodesDict) {
                    nodesDict[key].update(ctx, mouseX.value, mouseY.value)
                    if (nodesDict[key].curRadius > nodesDict[key].initRadius) {
                        for (let i = 0; i < linesArray.length; i++) {
                            linesArray[i].text = undefined
                            linesArray[i].color = util.grey
                        }
                        var nodePosition = getNodePosition(nodesDict[key].id)
                        const nodeId = 'x' + nodePosition['layerNumber'] + nodePosition['nodeIndex']
                        const selectedNode = nodesDict[nodeId]
                        selectedNodeLineChildren = selectedNode.lineChildren
                        for (const key in selectedNodeLineChildren) {
                            console.log(step.value)
                            const childLine = selectedNodeLineChildren[key]
                            var textInfo = {
                                text: weights[key][step.value],
                                size: 'bold ' + 12 / 493 * canvas.width + 'px',
                                color: util.black
                            }
                            childLine.setText(new Text(textInfo), 0, -5)
                            childLine.color = util.wineRed
                        }
                    }
                }

                // console.log(selectedNodeLineChildren, 'selectedNodeLineChildren')
                if (selectedNodeLineChildren !== undefined) {
                    for (const key in selectedNodeLineChildren) {
                        // console.log(step.value, 'step')
                        const childLine = selectedNodeLineChildren[key]
                        const textInfo = {
                            text: weights[key][step.value],
                            size: 'bold ' + 12 / 493 * canvas.width + 'px',
                            color: util.black
                        }
                        childLine.setText(new Text(textInfo), 0, -5)
                    }
                }
            }
            init(canvas.width, canvas.height)
            animate()
        })

        return {
            mouseX,
            mouseY,
            mouseMove,
            onMounted,
            step,
            train,
            oneStep,
            resetStep
        }
    }
}
</script>

<style scoped>
#csCanvasContainter {
    display: block;
    width: 100%;
    height: 500px;
    border: 1px solid grey;
}

#csCanvas {
    border: 0px solid red;
}

.btnGroup {
    width:fit-content;
    margin: auto;
    margin-bottom: 12px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    font-family: "Open Sans", sans-serif;
    color: #0f0f0f;
    font-size: 17.2px;
    font-weight: 400;
}

.stepValue {
    margin-left: 20px;
    font-family: "Open Sans", sans-serif;
    color: #0f0f0f;
    font-size: 16.2px;
    font-weight: 400;
}

#btnStep, #btnTrain, #btnReset {
    font-family: "Open Sans", sans-serif;
    color: #0f0f0f;
    font-size: 15.2px;
    font-weight: 400;
}

</style>
