<template>
    <div class="content">
        <h1 id="lec2-topic"> Neural Nets as Universal Approximators </h1>
        <h2 id="lec2-sec-1"> Brain and Neural Network </h2>
        <!-- http://www.ifc.unam.mx/Brain/nenet.htm -->
        <p> In the brain, a typical <strong>neuron</strong> collects signals from others through a host of fine structures called <strong>dendrites</strong>.
            The neuron sends out spikes of electrical activity through the <strong>axon</strong> (the output and conducting structure)
            which can split into thousands of branches. At the end of each branch, a <strong>synapse</strong> converts the activity from
            the axon into electrical effects that inhibit or excite activity on the contacted (target) neuron. When a
            neuron receives excitatory input that is sufficiently large compared with its inhibitory input, it sends a
            spike of electrical activity (an action potential) down its axon [1].
        </p>
        <img src="./assets/neuron.jpg" alt="">

        <p> Neural networks, in fact, mimic the brain structure and its working principle. To understand how a network actually works, we need to
            first take a look at its very basic unit - <strong>Perceptron</strong> (i.e. the neuron of brain)
        </p>

        <h2 id='lec2-sec-2'> Perceptron </h2>

        <p> Perceptron, one of the oldest classifiers on this planet, is proposed by Frank Rosenblatt in 1957 at Cornell Aeronautical Laboratory.
            Despite its long history, perceptron has a simple structure and is easy to learn. Mastering its working theory would be the key to
            your future success in this course.
        </p>

        <p> Perceptron mimics the neuron of the human brain, but with a simplified structure: </p>

        <Perceptron />

        <p> For the purpose of demonstration, we only draw two input nodes in the graph. But in the general case, there could be <b> any </b>
            number of inputs
            <span class="sub_text">x<sub>1</sub></span>,
            <span class="sub_text">x<sub>2</sub></span>,
            ...
            <span class="sub_text">x<sub>n</sub></span>.
            Each input <b><span class="sub_text">x<sub>i</sub></span></b> (i = 1, 2, ..., n) is connected to the perceptron main body and their connection
            strength is controlled by a corresponding weight <b><span class="sub_text">w<sub>i</sub></span></b>. Another special input to the main body is the bias
            <b> b </b> which controls the difficulty of firing (the smaller the b is, the harder the perceptron would fire). Then the intermidiate output <b> z </b>
            (the weighted sum of inputs, and bias) out of the main body is computed by:
        </p>

        <img width="100" src="./assets/z_formula.png" alt="">

        <p> Later on, z will be passed through a 'threshold gate', which determines the final output y using the following rule: </p>

        <ul id="y_if">
            <li>y = 1, if z &gt;= 0</li>
            <li>y = 0, otherwise </li>
        </ul>

        <p> That is it! This is how perceptron works. Now try to play around those input boxes at the bottom of Figure 1 by tuning the values of
            <span class="sub_text">x<sub>1</sub></span>,
            <span class="sub_text">x<sub>2</sub></span>,
            <span class="sub_text">w<sub>1</sub></span>,
            <span class="sub_text">w<sub>2</sub></span>,
            to see if you can fire the perceptron. Once the perceptron fires, the output
            node would turn into <span class="red"> red </span> from <span class="grey"> grey </span>.
        </p>

        <h3 id='lec2-sec-2-1'> 'Soft' Perceptron </h3>
        <p> Instead of using the threshold gate, we could choose a "squashing" function to compress the intermidiate output z.
            This kind of squashing function is also called <strong> activation </strong>. There are many types of activations, but now we will only introduce
            three most popular ones:
        </p>

        <div id="booleanBtnGroup" style="margin-top: 50px">
            <el-radio-group v-model="activationBtnGroup1" size="medium">
                <el-radio-button label="Sigmoid" @click="S_T_R='Sigmoid'"></el-radio-button>
                <el-radio-button label="Tanh" @click="S_T_R='Tanh'"></el-radio-button>
                <el-radio-button label="ReLU" @click="S_T_R='ReLU'"></el-radio-button>
            </el-radio-group>
        </div>

        <div class="activation_graphs">
            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="S_T_R==='Sigmoid'">
                    <div> <img class='activation_img' src="@/notes/Lec2/assets/sigmoid.png"/> </div>
                    <!-- <p class='boolean_text'>
                        AND gates will fire if and only if two inputs are both 1s. Because if any of the input is not 1, the weighted sum would be smaller
                        than the threshold 2, so the gate will not fire.
                    </p> -->
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="S_T_R==='Tanh'">
                    <div> <img class='activation_img' src="@/notes/Lec2/assets/tanh.png"/> </div>
                    <!-- <p class='boolean_text'>
                        OR gates will fire if any of the two inputs is 1. Since compared to AND gates, OR gates' threshold value is only 1.
                    </p > -->
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="S_T_R==='ReLU'">
                    <div> <img class='activation_img' src="@/notes/Lec2/assets/relu.png"/> </div>
                    <!-- <p class='boolean_text'>
                        OR GATEs are straight forward,
                    </p> -->
                </div>
            </transition>
        </div>

        <p>
            The main purpose of using activations is to introduce <b>non-linearity</b>. Well, non-linearity, fancy word, but what is it? Don't worry, I promise you this concept
            will be well covered in later lectures. Now all you need to know is that there is something named this.
        </p>

        <h2 id='lec2-sec-3'> Multi-layer Perceptron (MLP) </h2>
        <p> Until now, you've already mastered the main concept of perceptron -- the very basic unit of almost all complex networks. Then, let's dive into
            a much more interesting topic: Multi-layer Perceptron (MLP).
        </p>

        <p> MLP is nothing but a network composed of multiple layers of perceptrons. Each red circle in Figure 2 represents a perceptron. Try to tune the
            number of layers and the number of perceptrons in each layer and the graph would respond.
        </p>

        <MLP />

        <h2 id='lec2-sec-4'> MLP as Universal Boolean Function</h2>
        <p> Now, you've already had a basic understanding of MLP structure. Next, let's talk about what MLP can do. Although MLP is just a couple of layers
            of perceptrons stacked on each other, we shall never underestimate its capabilities. Believe it or not, it is powerful enough to handle many very complicated
            tasks, such as object detection, speech recognition, face recognition, etc. But we will not focus on these complex tasks for now (sorry!). Instead, I will introduce a
            slightly simple job that MLP can do: <b>representing any Boolean function</b> (still very impressive!).
        </p>

        <h3 id='lec2-sec-4-1'> Perceptron as a Boolean Gate </h3>
        <p> To prove that MLP can represent any Boolean function, let's first take a look at what its basic component - perceptron - can do. Based on what you've learned
            in the Perceptron section, I believe it is not hard to understand that perceptron could be used to represent <b>AND</b>, <b>OR</b>, <b>NOT</b> gates. Please switch the buttons below
            to see their structures.
        </p>

        <div id="booleanBtnGroup" style="margin-top: 50px">
            <el-radio-group v-model="booleanBtnGroup1" size="medium">
                <el-radio-button label="AND" @click="AND_OR_NOT='AND'"></el-radio-button>
                <el-radio-button label="OR" @click="AND_OR_NOT='OR'"></el-radio-button>
                <el-radio-button label="NOT" @click="AND_OR_NOT='NOT'"></el-radio-button>
            </el-radio-group>
        </div>

        <div class="Boolean_graphs">
            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="AND_OR_NOT==='AND'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/AND.png"/> </div>
                    <p class='boolean_text'>
                        An AND gate will fire if and only if two inputs are both 1s. Because if any of the input is not 1, the weighted sum would be smaller
                        than the threshold 2, so the gate will not fire.
                    </p>
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="AND_OR_NOT==='OR'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/OR.png"/> </div>
                    <p class='boolean_text'>
                        Compared to AND gates, an OR gate' threshold value is only 1, so it will fire if any of the two inputs is 1.
                    </p >
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="AND_OR_NOT==='NOT'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/NOT.png"/> </div>
                    <p class='boolean_text'>
                        A NOT gate is straight forward, any input will be negated.
                    </p>
                </div>
            </transition>
        </div>

        <br>
        <p>Besides normal AND, OR, NOT gates, percepron is also able to represent more general gates: universal AND gate (<b>UAG</b>), universal OR gate (<b>UOG</b>),
            generalized majority gate (<b>GMG1</b>, <b>GMG2</b>). Again, switch the buttons to see their structures.
        </p>

        <div id="booleanBtnGroup" style="margin-top: 50px">
            <el-radio-group v-model="booleanBtnGroup2" size="medium">
                <el-radio-button label="UAG" @click="booleanGraph='UAG'"></el-radio-button>
                <el-radio-button label="UOG" @click="booleanGraph='UOG'"></el-radio-button>
                <el-radio-button label="GMG1" @click="booleanGraph='GMG1'"></el-radio-button>
                <el-radio-button label="GMG2" @click="booleanGraph='GMG2'"></el-radio-button>
            </el-radio-group>
        </div>

        <div class="Boolean_graphs">
            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="booleanGraph==='UAG'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/universal_and.png"/> </div>
                    <p class='boolean_text'>
                        Compared to AND gate, the universal AND gate can receive more than two binary inputs (0 or 1).
                        The gate will fire <b> if and only if
                        <span class="sub_text">x<sub>1</sub></span> ..
                        <span class="sub_text">x<sub>L</sub></span>
                        are all 1s and others are all 0s. </b>
                        The weighted sum of
                        <span class="sub_text">x<sub>L+1</sub></span> ..
                        <span class="sub_text">x<sub>N</sub></span>,
                        is at most 0 (since their corresponding weights are -1). If any of
                        <span class="sub_text">x<sub>1</sub></span> ..
                        <span class="sub_text">x<sub>L</sub></span>
                        is 0, the total weighted sum is smaller than L, so the gate won't fire. Similarly,
                        the weighted sum of
                        <span class="sub_text">x<sub>1</sub></span> ..
                        <span class="sub_text">x<sub>L</sub></span>,
                        is at most L. If any of
                        <span class="sub_text">x<sub>L+1</sub></span> ..
                        <span class="sub_text">x<sub>N</sub></span>
                        is 1, the total weighted sum would be smaller than L and again the gate won't fire.
                    </p>
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="booleanGraph==='UOG'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/universal_or.png"/> </div>
                    <p class='boolean_text'>
                        Apart from universal AND gates, a universal OR gate can be designed based on similar ideas. The weighted sum of
                        <span class="sub_text">x<sub>1</sub></span> ..
                        <span class="sub_text">x<sub>L</sub></span>
                        is at least 0, while that of
                        <span class="sub_text">x<sub>L+1</sub></span> ..
                        <span class="sub_text">x<sub>N</sub></span>
                        is at least L-N. That is, the total weighted sum is at least L-N.
                        Therefore, to surpass the threshold L-N+1, the gate will fire
                        <b> if and only if any one of
                            <span class="sub_text">x<sub>1</sub></span> ..
                            <span class="sub_text">x<sub>L</sub></span>
                            is 1 or any one of
                            <span class="sub_text">x<sub>L+1</sub></span> ..
                            <span class="sub_text">x<sub>N</sub></span>
                            is 0.
                        </b>
                    </p >
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="booleanGraph==='GMG1'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/majority1.png"/> </div>
                    <p class='boolean_text'>
                        We also have a generalized majority gate, which fires <b>if and only if at least K inputs are 1s</b>. If only K-1
                        inputs are 1s, then the total weighted sum would be K-1 &#60; K, and the gate will not fire.
                    </p>
                </div>
            </transition>

            <transition enter-active-class='animate__animated animate__fadeIn'>
                <div v-show="booleanGraph==='GMG2'">
                    <div> <img class='boolean_img' src="@/notes/Lec2/assets/majority2.png"/> </div>
                    <p class='boolean_text'>
                        The generalized majority gate can be represented in another form. If
                        <span class="sub_text">x<sub>1</sub></span> ..
                        <span class="sub_text">x<sub>L</sub></span>
                        are all 0s, their weighted sum is at least 0, while if
                        <span class="sub_text">x<sub>L+1</sub></span> ..
                        <span class="sub_text">x<sub>N</sub></span>
                        are all 1s, their weighted sum is at least L-N. That is, the total weighted sum has a minimum of L-N. So this gate will fire
                        <b> if and only if the total number of
                            <span class="sub_text">x<sub>1</sub></span> ..
                            <span class="sub_text">x<sub>L</sub></span>
                            that are 1s and
                            <span class="sub_text">x<sub>L+1</sub></span> ..
                            <span class="sub_text">x<sub>N</sub></span> that are 0s is at least K.
                        </b>
                    </p>
                </div>
            </transition>
        </div>

        <h3 id='lec2-sec-4-2'> Limitation of Perceptron </h3>
        <p> However, perceptron is not enough to present all gates. For example, an XOR gate (fires only if two inputs are different, 01 or 10) cannot be represented
            by perceptrons. To solve this problem, MLP is introduced, and it simply adds one more layer of nodes.
        </p>
        <div class='boolean_img'> <img src="@/notes/Lec2/assets/xor.png"/> </div>

        <p> So far, we've proved that perceptron can represent AND, OR, NOT, universal AND, universal OR, generalized majority gates, but cannot represent XOR gate.
            However, XOR can be represented by MLP by simply adding one layer. Since perceptron is a subset of MLP, so MLP is able to compute any boolean functions;
            therefore, <b>MLPs are universal boolean functions.</b> </p>

        <h2 id='lec2-sec-5'> How many layers for a Boolean MLP? </h2>

        <p> Let's say we have four variables W, X, Y and Z, each of which can be either 0 or 1.
            Suppose we have a case in which there are seven different combinations of variables such that
            each combination outputs 1, and the other combinations output 0.
            If we put these 7 combinations into a table, it is called Truth Table (see below). Each row of
            the table, which is called a clause, represents a combination of variables with output = 1.
        </p>

        <p>
            The following section will explain how to map each row of Truth Table to each hidden neuron in our neural
            network. Please move your mouse to hover each row of the table below and you will see corresponding hidden neuron
            is connected.
        </p>

        <TruthTable />
        <br>
        <p> After playing around the table and the neural net, it is clear that each row can be represented by a single hidden neuron.
            Because the truth table has only 7 rows, we can align 7 hidden neurons into a single layer. Therefore, we only have 1 hidden
            layer for this neural network!
        </p>

        <h2 id='lec2-sec-6'> Reducing Boolean Function </h2>
        <p> Now, we would like to do something fancy: <b>to reduce the number of demanded neurons by grouping two variables into a single one</b>. For instance,
            we can group W and X to a single variable WX with four possible values 00, 01, 11 and 10. We also group Y and Z
            to YZ with the same values. The above Truth Table can be simplified into another form: Karnaugh Map (the
            red-grey map below, red -> 1, grey -> 0).
        </p>

        <p> Karnaugh Map can reduce the complexity of neural net by grouping certain rows of the above Truth Table into a sinlge one.
            You can check or uncheck the group box to see how it works.
        </p>

        <KarnaughMap />

        <h2 id='lec2-sec-7'> Largest Irreducible DNF </h2>
        <p> However, not all truth tables can be represented in a reduced form. In the left Karnaugh Map below, there are no proximal red blocks
            that can be grouped togther. In other words, we have to utilize one clause to represent each block, so the net needs 8 hidden neurons.
            Under this scenario, we <b>cannot</b> reduce the complexity of its corresponding neural net.
        </p>
        <p> In general, the number of hidden neurons equals to <b>2<sup>N-1</sup></b>
            where N equals the number of input variables, so the number of hidden neurons will grow exponentially as N increases.
            When we have four variables W, X, Y, Z, we need 2<sup>4-1</sup> = 8 hidden neurons.
        </p>

        <IrreducibleMap />

        <p> In fact, this irreducible map can be represented in a series of <strong>XOR</strong> gates as shown in the equation below:</p>
        <p class="eqn">O = W &#8855; X &#8855; Y &#8855; Z</p>
        <p> We also know that to represent an XOR gate, 3 neurons are needed. Therefore, if we use multiple hidden layers,
            we can simply use 9 neurons! To generalize, if we have N input variables, we only need <strong>3(N-1)</strong> neurons
            to represent the irreducible map if we stack hidden layers. The complexity of neural net now is linear in N instead
            of exponential!
        </p>

        <p>If we do one step further by paring two XORs, such as</p>
        <p class="eqn"> O = (W &#8855; X) &#8855;  (Y &#8855; Z) </p>
        <p>instead of</p>
        <p class="eqn"> O = (((W &#8855; X) &#8855; Y) &#8855; Z)</p>
        <p>the total number of layers can be reduced from 2(N-1) to <strong>2log<sub>2</sub>N</strong>.
           (See the reduced layer MLP right beside the Karnaugh Map!)
        </p>
        <br>
        <p>Therefore, the <strong>DEPTH</strong> of a MLP is very <strong>IMPORTANT</strong>!</p>

        <h2 id='lec2-sec-8'> MLPs are Universal Classifiers </h2>
        <h3 id='lec2-sec-8-1'> A perceptron can be seen as a binary classifier. </h3>
        <p> So far, we know that a perceptron finds a complex decision boundary over a high-dimensional space. Specifically,
            perceptrons focus on the operation of real values, such as binary operators. For example, a simple classifier can be defined as follows.
            To correctly separate two groups of points, the weights should be carefully chosen.<br>
        </p>

        <Binary />

        <h3 id='lec2-sec-8-2'> MLPs can be used to compose any complex polygon. </h3>
        <p> Based on the combination of perceptrons, MLPs can compose any complex polygon. For instance, to compose a regular pentagon, it can be easily represented using
            5 perceptrons and an AND operator as demonstrated in the Fig. 2.</p>

        <Pentagon />
        <h3 id='lec2-sec-8-3'> MLPs can represent any complicated decision boundaries. </h3>
        <p> Different from complex polygons, a circle is much harder to be rendered. As the number of perceprtons grows for a one-hidden-layer MLP, the enclosed region is more similar to a circle.
            As demonstrated in the Fig. 3, a very large number of neurons results in a near perfect circle. Try different numbers below (>= 3) to observe the discrepancy.<br>
        </p>
        <Polygon />

        <p> In addition, any complicated arbitary figure can be represented using a series of circles. By fitting in numerous small circles, it can achieve arbitrary precision. </p>
        <img class='haocheng_img' src="./assets/complexGraph.png"/>
        <p>
            Therefore, single-hidden-layer MLPs can model any classification boundary. In short, <b>MLPs are universal classifiers</b>. And again, even though single-hidden-layer MLPs can be universal boolean functions or universal classifiers, it may require infinite neurons. Deep networks can effectively reduce the required number of neurons, which is exponentially smaller.
        </p>

        <!-- Haocheng -->
        <h2 id='lec2-sec-9'> MLPs as Universial Approximators</h2>
        <p> One-hidden-layer MLPs can model any function to arbitary precision.</p>
        <img class='haocheng_img' src="./assets/regre.png"/>
        <p> As the picture shows above, one percepron can represent a step signal and two can represent a pulse signal. Therefore,  using arbitrary
            number of perceptrons, we can approximate a continuous curve to any precision. That is, a one-hidden-layer MLP with infinite neurons can be a universal approximator.
        </p>
        <p> To approximate high dimensional functions, we only need to do add more inputs.</p>
        <h3 id='lec2-sec-9-1'> Activation function and non-linearity</h3>
        <p> Although we have proved that a one-hidden-layer MLP with infinite neurons can be a universal approximator, practically we can never create infinite neurons.
            Based on what we have discussed, the required number of neurons can be drastically reduced by introducing deep neural network.
            However, simply stacking multiple layers makes no salient difference. A multiple-hidden-layer MLP without activations doesn't
            have any non-linear property, which means that it is actually equivalent to a single-hidden-layer MLP.
        </p>
        <p> For instance, one neuron can model a linear function y = w<sub>1</sub>x + b<sub>1</sub>. If we directly stack two neurons together, it represents a function
            y = w<sub>2</sub> (w<sub>1</sub>x + b<sub>1</sub>) + b<sub>2</sub> = w<sub>3</sub>x + b<sub>3</sub>, which is still a linear function that can be achieved using only one neuron.
            On the other hand, if we add an activation function to each neuron, the output is no longer an affined transformation of inputs. That is because activation functions introduce non-linearity to the MLP.
            Moreover, if we take a deeper look at the output domain, we can see that the MLP now can represent a non-linear function with finite neurons.
        </p>
        <h3 id='lec2-sec-9-2'> Depth and width are both necessary </h3>
        <p> Previous discussion showed that a single-hidden-layer MLP with infinite neurons is a universal function approximator, and deeper networks require
            far fewer neurons for the same approximation error. However, depth also has some limitations. In addition to introduce more layers, we also need to ensure the number of
            neuron at <b>each</b> layer is sufficient, and thus it can pass sufficient information to the next layer. Insufficient neurons at any layer may lead to insufficient capacity.
        </p>
        <img class='haocheng_img' src="./assets/suffi.png"/>
        <p> For example, we can use one perceptron to divide a 2-D plane into two parts. Therefore, at least 16 neurons are required to represent
            the complicated region shown above, as the pattern is composed of 16 crosswires.
        </p>
        <p> If we use the threshold function as the activation function, a single-hidden-layer MLP with 16 neurons is far from enough.
            Theoretically, we can precisely represent this pattern unless there are 40 neurons in the second layer.</p>
        <p> Another factor that can help improve neuron sufficiency is <b>continuous</b> activation function. For instance, if we select threshold gate as activation, the ouput would be either 0
            or 1, so the neuron can only pass 0 or 1 to the next neuron (the information has been blocked). On the other hand, with continuous activation functions, it passes more information to subsequent layers.
        </p>
        <img class='haocheng_img' src="./assets/acti.png"/>
        <p> Here's three activation function. From left to right, each function is more graded.</p>
        <p> In conclusion, <b>wider networks with more graded activation functions are more expressive.</b></p>
        <br><br><br><br>
    </div>
</template>

<script>
