h2 {
    display: block;
    margin-block-start:2.2em;
    margin-block-end: 0.8em;
    color: #db1c4c;
    font-weight: 400;
}

h3 {
    display: block;
    margin-block-start: 2.2em;
    font-weight: 600;
}

ul li{
   border: 0px solid red;
   padding-left: 30px;
   list-style: none;
   font-weight: 400;
}

img {
    width: 100%;
    max-width: 100%;
    height: auto;
}

#perceptronCanvasContainter {
    display: block;
    width: 100%;
    height: 550px;
    border: 1px solid rgb(190, 187, 187);
}

canvas {
    border: 0px solid red;
}

.perceptron_value {
    table-layout:fixed;
    border: 1px solid black;
}

.perceptron_value tr td{
    border: 1px solid black;
    text-align: center;
}

input {
    width: 80%;
    font-family: "Lora", sans-serif;
    font-size: 15px;
    text-align: center;
    padding: 3px;
    margin: 0% 10%;
}

.perceptron_grid {
    display: grid;
    grid-template-rows: repeat(2, 50%);
    grid-template-columns: repeat(5, 20%);
    border: 0px solid black;
    padding-top: 12px;
    padding-bottom: 12px;
}
.perceptron_para {
    text-align: center;
    font-family: "Lora", sans-serif;
    font-size: 16px;
}

#y_if li{
    margin-left: 6%;
    padding: 10px;
    font-size: 18px;
}

.sub_text {
    font-size: 20.2px;
}

.Boolean_graphs {
    height: auto;
    border: 0px solid grey;
    border-radius: 0px;
    overflow-x: hidden;
}

#boolean_grid {
    display: grid;
    grid-template-rows: repeat(2, 50%);
    grid-template-columns: repeat(3, 33.3%);
    border: 0px solid black;
    padding-top: 8px;
    padding-bottom: 8px;
}

#and_or_not_grid {
    display: grid;
    grid-template-rows: repeat(1, 100%);
    grid-template-columns: repeat(3, 33.3%);
    border: 0px solid black;
    padding-top: 8px;
    padding-bottom: 8px;
}

#boolean_btn {
    padding: 8px;
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 2%;
    margin-bottom: 1%;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: white;
    background-color:#db1c4c;
    border: none;
    cursor: pointer;
    transition-duration: 0.4s;
}

#boolean_btn:hover {
    background-color: #c71743;
}

.boolean_text {
    padding-bottom: 1.5%;
}

